import React, { useEffect, useState } from 'react';
import {
    Box,
    Typography,
    Paper,
    Table,
    TableHead,
    TableRow,
    TableCell,
    TableBody,
    Divider,
    Chip,
} from '@mui/material';
// import { getOrdersById } from '../../services/UsersSevices';
import { mockGetOrderByIdAPI } from '../../services/mockOrderTest';

const AdminOrderDetail = ({ orderId }) => {
    const [order, setOrder] = useState(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    useEffect(() => {
        if (!orderId) {
            console.warn('OrderDetail: orderId is missing or invalid');
            return;
        }

        const fetchOrder = async () => {
            setLoading(true);
            setError(null);
            try {
                // Use mock API for testing - switch to getOrdersById for production
                const data = await mockGetOrderByIdAPI(orderId);
                // const data = await getOrdersById(orderId);
                
                if (!data) {
                    setError('Khng tm thy d liu n hng');
                    setOrder(null);
                } else {
                    setOrder({
                        ...data,
                        orderDetails: data.orderDetails || data.items || [],
                        statuses: data.statuses || [],
                    });
                }
            } catch (err) {
                console.error('OrderDetail: Error fetching order:', err);
                setError('Li khi ly d liu n hng');
                setOrder(null);
            }
            setLoading(false);
        };

        fetchOrder();
    }, [orderId]);

    const formatDateTime = (dateStr) => {
        if (!dateStr) return '';
        return new Date(dateStr).toLocaleString('vi-VN');
    };

    if (loading) return <Typography>ang ti d liu n hng...</Typography>;

    if (error) return <Typography color="error">{error}</Typography>;

    if (!order) return <Typography>Khng c d liu n hng  hin th.</Typography>;

    const statusLabelMap = {
        Pending: 'Ch xc nhn',
        Processing: 'ang iu tr',
        Completed: 'Hon tt',
        Cancelled: ' hy',
        Refund: 'Hon tin',
        // Payment status mapping for backward compatibility
        Delivered: ' thanh ton',
    };

    const getStatusColor = (status) => {
        switch (status) {
            case 'Pending':
                return '#9E9E9E'; // Gray
            case 'Processing':
                return '#FF9800'; // Orange
            case 'Completed':
            case 'Delivered':
                return '#4CAF50'; // Green
            case 'Cancelled':
                return '#F44336'; // Red
            case 'Refund':
                return '#FF6F3C'; // Orange-red
            default:
                return '#9E9E9E';
        }
    };

    return (
        <Box sx={{ p: 4 }}>
            <Typography variant="h5" sx={{ mb: 2, fontWeight: 600 }}>
                Chi tit n hng #{order.id}
            </Typography>

            <Paper sx={{ p: 3, mb: 3 }}>
                <Typography variant="subtitle1"> Bnh nhn: {order.customer?.name || 'N/A'}</Typography>
                <Typography variant="subtitle1"> T vn vin: {order.designer?.name || 'N/A'}</Typography>
                <Typography variant="subtitle1"> Chuyn khoa: {order.designer?.specialty || 'N/A'}</Typography>
                <Typography variant="subtitle1"> Tui: {order.customer?.age || 'N/A'}</Typography>
                <Typography variant="subtitle1"> Gii tnh: {order.customer?.gender || 'N/A'}</Typography>
                <Typography variant="subtitle1">
                     Tng chi ph: {order.orderPrice?.toLocaleString() || 0}
                </Typography>
                <Typography variant="subtitle1">
                     Trng thi hin ti:
                    <Chip
                        label={statusLabelMap[order.status] || order.status}
                        sx={{
                            ml: 1,
                            color: '#fff',
                            bgcolor: getStatusColor(order.status),
                            minWidth: 120,
                            textAlign: 'center'
                        }}
                    />
                </Typography>
                <Typography variant="subtitle1">
                     Thanh ton: 
                    <Chip
                        label={order.isPaid ? ' thanh ton' : 'Cha thanh ton'}
                        sx={{
                            ml: 1,
                            color: '#fff',
                            bgcolor: order.isPaid ? '#4CAF50' : '#F44336',
                            minWidth: 120,
                            textAlign: 'center'
                        }}
                    />
                </Typography>
            </Paper>

            <Typography variant="h6" sx={{ mb: 1 }}>
                 Danh sch dch v y t
            </Typography>
            <Table>
                <TableHead>
                    <TableRow>
                        <TableCell>Tn dch v</TableCell>
                        <TableCell>M dch v</TableCell>
                        <TableCell>Gi</TableCell>
                        <TableCell>S lng</TableCell>
                        <TableCell>Thnh tin</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {order.orderDetails.map((detail, index) => (
                        <TableRow key={index}>
                            <TableCell>{detail.product?.name || 'N/A'}</TableCell>
                            <TableCell>
                                <Typography 
                                    variant="body2"
                                    sx={{ 
                                        fontFamily: 'monospace',
                                        fontSize: '0.875rem',
                                        fontWeight: 500,
                                        color: '#1976D2'
                                    }}
                                >
                                    {detail.product?.code || detail.product?.id || `SV${String(index + 1).padStart(3, '0')}`}
                                </Typography>
                            </TableCell>
                            <TableCell>{detail.product?.price?.toLocaleString() || 0}</TableCell>
                            <TableCell>{detail.quantity || 0}</TableCell>
                            <TableCell>{detail.detailPrice?.toLocaleString() || 0}</TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>

            <Divider sx={{ my: 4 }} />

            <Typography variant="h6" sx={{ mb: 1 }}>
                 Lch s trng thi iu tr
            </Typography>
            {order.statuses.map((s, index) => (
                <Box key={index} sx={{ mb: 1 }}>
                    <Chip
                        label={statusLabelMap[s.name] || s.name}
                        sx={{
                            mr: 2,
                            color: '#fff',
                            bgcolor: getStatusColor(s.name),
                            minWidth: 120,
                            textAlign: 'center'
                        }}
                    />
                    <Typography component="span">{formatDateTime(s.time)}</Typography>
                </Box>
            ))}
        </Box>
    );
};

export default AdminOrderDetail;
