/**
 * Cache v versioning utilities cho ng dng
 */

/**
 * Thm timestamp query cho development  trnh cache
 * @param {string} url - URL gc
 * @returns {string} - URL vi query timestamp
 */
export const addCacheBuster = (url) => {
  if (import.meta.env.DEV) {
    const separator = url.includes('?') ? '&' : '?';
    return `${url}${separator}v=${Date.now()}`;
  }
  return url;
};

/**
 * To URL vi version hash cho assets
 * @param {string} assetPath - ng dn asset
 * @param {string} version - Version string (optional)
 * @returns {string} - URL vi version
 */
export const getVersionedAssetUrl = (assetPath, version = null) => {
  // Trong production, Vite t ng hash
  if (import.meta.env.PROD) {
    return assetPath;
  }
  
  // Trong development, thm version manual nu cn
  if (version) {
    const separator = assetPath.includes('?') ? '&' : '?';
    return `${assetPath}${separator}v=${version}`;
  }
  
  return addCacheBuster(assetPath);
};

/**
 * Clear browser cache programmatically
 */
export const clearCache = async () => {
  try {
    if ('caches' in window) {
      const cacheNames = await caches.keys();
      await Promise.all(
        cacheNames.map(cacheName => caches.delete(cacheName))
      );
      console.log('Cache cleared successfully');
    }
    
    // Reload trang sau khi clear cache
    window.location.reload(true);
  } catch (error) {
    console.error('Error clearing cache:', error);
  }
};

/**
 * Get app version t package.json (nu c)
 */
export const getAppVersion = () => {
  return import.meta.env.VITE_APP_VERSION || '1.0.0';
};

/**
 * Check v so snh version, clear cache nu cn
 */
export const checkVersionAndClearCache = () => {
  const currentVersion = getAppVersion();
  const storedVersion = localStorage.getItem('app_version');
  
  if (storedVersion && storedVersion !== currentVersion) {
    console.log(`Version changed from ${storedVersion} to ${currentVersion}`);
    clearCache();
  }
  
  localStorage.setItem('app_version', currentVersion);
};

/**
 * Preload critical resources vi versioning
 */
export const preloadResources = (resources = []) => {
  resources.forEach(resource => {
    const link = document.createElement('link');
    link.rel = 'prefetch';
    link.href = getVersionedAssetUrl(resource);
    document.head.appendChild(link);
  });
};

/**
 * Service Worker cache management
 */
export const updateServiceWorker = async () => {
  if ('serviceWorker' in navigator) {
    try {
      const registration = await navigator.serviceWorker.getRegistration();
      if (registration) {
        await registration.update();
        console.log('Service Worker updated');
      }
    } catch (error) {
      console.error('Error updating Service Worker:', error);
    }
  }
};

/**
 * Force reload vi cache bypass
 */
export const forceReload = () => {
  // Bypass cache khi reload
  window.location.reload(true);
};

export default {
  addCacheBuster,
  getVersionedAssetUrl,
  clearCache,
  getAppVersion,
  checkVersionAndClearCache,
  preloadResources,
  updateServiceWorker,
  forceReload
};
