import React, { useState, useEffect } from 'react';
import { useCycle } from '../../../context/CycleContext';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../../../context/AuthContext';
import notificationService from '../../../services/NotificationService';
import {
  Calendar,
  Plus,
  Smile,
  Frown,
  Meh,
  Heart,
  Droplets,
  Bell,
  BookOpen,
  MessageCircle,
  TestTube,
  Settings,
  History,
  TrendingUp,
  Info,
  ChevronRight,
  Activity,
  Moon,
  Sun,
  CloudRain,
  Sparkles,
  Menu,
  X,
  User,
  LogOut,
} from 'lucide-react';
import {
  format,
  startOfMonth,
  endOfMonth,
  eachDayOfInterval,
  isSameMonth,
  isSameDay,
  addMonths,
  subMonths,
} from 'date-fns';
import { getTodayFormatted } from '../../../utils/dateUtils';
import CycleInsights from '../../../components/CycleInsights';

import styles from '../../HomePage.module.css';
import './index.css';

const CycleTracking = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [showUserMenu, setShowUserMenu] = useState(false);
  const { user, logout, isAuthenticated } = useAuth();
  const navigate = useNavigate();

  const {
    cycleData,
    predictions,
    notifications,
    updateCycleData,
    addSymptom,
    addMood,
    addNote,
    getDaysUntilNextPeriod,
    getDaysUntilOvulation,
    isInFertilityWindow,
    getDataForDate,
    getCyclePhaseForDate,
    getHealthRecommendations,
  } = useCycle();

  const [selectedDate, setSelectedDate] = useState(new Date());
  const [viewingMonth, setViewingMonth] = useState(new Date());
  const [showAddModal, setShowAddModal] = useState(false);
  const [showHistoryModal, setShowHistoryModal] = useState(false);
  const [showPeriodForm, setShowPeriodForm] = useState(false);
  const [showAnalyticsModal, setShowAnalyticsModal] = useState(false);
  const [showExportModal, setShowExportModal] = useState(false);
  const [showSettingsModal, setShowSettingsModal] = useState(false);
  const [modalType, setModalType] = useState('period');
  const [periodFormData, setPeriodFormData] = useState({
    startDate: getTodayFormatted(),
    expectedLength: cycleData.periodLength || 5,
  });
  const [newNoteText, setNewNoteText] = useState('');
  const [notificationPermission, setNotificationPermission] =
    useState('default');

  const today = new Date();
  const monthStart = startOfMonth(viewingMonth);
  const monthEnd = endOfMonth(viewingMonth);
  const monthDays = eachDayOfInterval({ start: monthStart, end: monthEnd });

  const selectedDateData = getDataForDate(selectedDate);
  const healthRecommendations = getHealthRecommendations();

  // Defensive check  m bo cycleData c y  fields cn thit
  const safeMode = !cycleData.symptoms || !cycleData.mood || !cycleData.notes;

  const handleLogout = () => {
    logout();
    setShowUserMenu(false);
    navigate('/');
  };

  const handleStartPeriod = () => {
    updateCycleData({
      lastPeriod: periodFormData.startDate,
      periodLength: parseInt(periodFormData.expectedLength),
    });
    setShowPeriodForm(false);
  };

  const handleAddNote = () => {
    if (newNoteText.trim()) {
      addNote(selectedDate, newNoteText.trim());
      setNewNoteText('');
      setShowAddModal(false);
    }
  };

  const moodIcons = {
    happy: { icon: Smile, color: 'text-green-500', label: 'Vui v' },
    sad: { icon: Frown, color: 'text-blue-500', label: 'Bun' },
    neutral: { icon: Meh, color: 'text-gray-500', label: 'Bnh thng' },
    excited: { icon: Sparkles, color: 'text-yellow-500', label: 'Phn khch' },
    tired: { icon: Moon, color: 'text-purple-500', label: 'Mt mi' },
    stressed: { icon: CloudRain, color: 'text-red-500', label: 'Cng thng' },
  };

  const symptomList = [
    'au bng',
    'au lng',
    'au u',
    'Bun nn',
    'Mt mi',
    'Thay i cm xc',
    'Tng cn',
    'Chut rt',
    'au ngc',
    'Mn trng c',
    'Chy mu bt thng',
    'Kh m o',
  ];

  const getPeriodPhase = date => {
    return getCyclePhaseForDate(date);
  };

  const getCurrentPhaseInfo = phase => {
    switch (phase) {
      case 'period':
        return {
          label: 'K kinh',
          icon: '',
          description: 'Lt nim mc t cung ang thot ra',
          advice:
            'Ngh ngi  gic, ung nhiu nc, s dng ming lt ph hp',
          color: '#e91e63',
          bgColor: 'linear-gradient(135deg, #ffebee, #fce4ec)',
        };
      case 'follicular':
        return {
          label: 'Giai on nang trng',
          icon: '',
          description: 'C th ang chun b cho chu k mi',
          advice:
            'Thi im tt  bt u hot ng th cht v ch  n lnh mnh',
          color: '#1976d2',
          bgColor: 'linear-gradient(135deg, #e3f2fd, #f1f8e9)',
        };
      case 'fertility':
        return {
          label: 'Thi k mu m',
          icon: '',
          description: 'Kh nng th thai cao nht trong chu k',
          advice: 'Nu c k hoch mang thai, y l thi im l tng',
          color: '#ad1457',
          bgColor: 'linear-gradient(135deg, #fce4ec, #f3e5f5)',
        };
      case 'ovulation':
        return {
          label: 'Rng trng',
          icon: '',
          description: 'Trng ang c gii phng t bung trng',
          advice:
            'C th cm thy au bng nh, nng lng cao v ham mun tng',
          color: '#e65100',
          bgColor: 'linear-gradient(135deg, #fff3e0, #fff8e1)',
        };
      case 'luteal':
        return {
          label: 'Giai on hong th',
          icon: '',
          description: 'C th ang ch i tn hiu mang thai',
          advice:
            'C th xut hin PMS, hy ch  n ch  n v tp th dc nh nhng',
          color: '#7b1fa2',
          bgColor: 'linear-gradient(135deg, #f3e5f5, #e8eaf6)',
        };
      case 'premenstrual':
        return {
          label: 'Tin kinh nguyt',
          icon: '',
          description: 'C th ang chun b cho k kinh tip theo',
          advice:
            'C th cm thy cng thng, hy thc hin cc hot ng th gin',
          color: '#3f51b5',
          bgColor: 'linear-gradient(135deg, #e8eaf6, #e3f2fd)',
        };
      default:
        return {
          label: 'Cha xc nh',
          icon: '',
          description: 'Cn thm d liu  xc nh giai on',
          advice: 'Hy ghi nhn thng tin chu k  c d on chnh xc hn',
          color: '#757575',
          bgColor: 'linear-gradient(135deg, #f5f5f5, #eeeeee)',
        };
    }
  };

  const currentPhaseInfo = getCurrentPhaseInfo(predictions.currentPhase);

  // Initialize notifications and check permissions
  useEffect(() => {
    const initNotifications = async () => {
      if ('Notification' in window) {
        const permission = await notificationService.requestPermission();
        setNotificationPermission(permission ? 'granted' : 'denied');

        if (permission && cycleData.lastPeriod) {
          notificationService.scheduleBrowserNotifications(cycleData);
        }
      }
    };

    initNotifications();
  }, [cycleData]);

  // Analytics calculations
  const getCycleAnalytics = () => {
    if (!cycleData.cycleHistory || cycleData.cycleHistory.length < 2) {
      return null;
    }

    const history = cycleData.cycleHistory;
    const cycleLengths = history.map(cycle => cycle.cycleLength);
    const avgLength =
      cycleLengths.reduce((sum, length) => sum + length, 0) /
      cycleLengths.length;
    const minLength = Math.min(...cycleLengths);
    const maxLength = Math.max(...cycleLengths);
    const variance =
      cycleLengths.reduce(
        (sum, length) => sum + Math.pow(length - avgLength, 2),
        0
      ) / cycleLengths.length;
    const regularity =
      variance < 4 ? 'u n' : variance < 9 ? 'Kh u' : 'Khng u';

    return {
      totalCycles: history.length,
      avgLength: Math.round(avgLength),
      minLength,
      maxLength,
      regularity,
      variance: Math.round(variance * 10) / 10,
    };
  };

  // Export data functionality
  const handleExportData = () => {
    const exportData = {
      cycleData,
      predictions,
      exportDate: new Date().toISOString(),
      version: '1.0',
    };

    const dataStr = JSON.stringify(exportData, null, 2);
    const dataBlob = new Blob([dataStr], { type: 'application/json' });
    const url = URL.createObjectURL(dataBlob);

    const link = document.createElement('a');
    link.href = url;
    link.download = `chu-ky-sinh-san-${format(new Date(), 'yyyy-MM-dd')}.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  };

  // Handle notification permission
  const handleEnableNotifications = async () => {
    const permission = await notificationService.requestPermission();
    setNotificationPermission(permission ? 'granted' : 'denied');

    if (permission && cycleData.lastPeriod) {
      notificationService.scheduleBrowserNotifications(cycleData);
      notificationService.createInAppNotification({
        type: 'system',
        title: 'Thng bo  c bt',
        message: 'Bn s nhn c nhc nh v chu k sinh sn.',
        priority: 'low',
      });
    }
  };

  const analytics = getCycleAnalytics();

  return (
    <div className={styles.homepage}>
      {/* Header - ng nht vi HomePage v Dashboard */}
      <header className={styles.header}>
        <div className={styles.container}>
          <div className={styles.headerContent}>
            <Link to="/" className={styles.logo}>
              <Heart className={styles.logoIcon} />
              <span className={styles.logoText}>Gynexa</span>
            </Link>

            <nav
              className={`${styles.nav} ${isMenuOpen ? styles.navOpen : ''}`}
            >
              <Link to="/" className={styles.navLink}>
                Trang ch
              </Link>
              <Link to="/dashboard" className={styles.navLink}>
                Dashboard
              </Link>
              <Link to="/cycle-tracking" className={styles.navLink}>
                Theo di chu k
              </Link>
              <Link to="/consultation" className={styles.navLink}>
                T vn
              </Link>
              <Link to="/sti-testing" className={styles.navLink}>
                Xt nghim
              </Link>
              <Link to="/qa" className={styles.navLink}>
                Hi p
              </Link>
            </nav>

            <div className={styles.headerActions}>
              {!isAuthenticated ? (
                <>
                  <Link to="/login" className={styles.btnOutline}>
                    ng nhp
                  </Link>
                  <Link to="/register" className={styles.btnPrimary}>
                    ng k
                  </Link>
                </>
              ) : (
                <div style={{ position: 'relative' }}>
                  <button
                    className={styles.btnOutline}
                    onClick={() => setShowUserMenu(!showUserMenu)}
                    style={{ display: 'flex', alignItems: 'center', gap: 8 }}
                  >
                    <User size={18} style={{ marginRight: 4 }} />
                    {user?.name || 'Ti khon'}
                  </button>
                  {showUserMenu && (
                    <div
                      style={{
                        position: 'absolute',
                        right: 0,
                        top: '110%',
                        background: '#fff',
                        border: '1px solid #e2e8f0',
                        borderRadius: 8,
                        boxShadow: '0 4px 16px rgba(0,0,0,0.08)',
                        minWidth: 180,
                        zIndex: 100,
                      }}
                    >
                      <Link
                        to="/dashboard"
                        style={{
                          display: 'flex',
                          alignItems: 'center',
                          gap: 8,
                          padding: 12,
                          textDecoration: 'none',
                          color: '#222',
                          fontWeight: 500,
                        }}
                        onClick={() => setShowUserMenu(false)}
                      >
                        <User size={16} /> Dashboard
                      </Link>
                      <Link
                        to="/profile"
                        style={{
                          display: 'flex',
                          alignItems: 'center',
                          gap: 8,
                          padding: 12,
                          textDecoration: 'none',
                          color: '#222',
                          fontWeight: 500,
                        }}
                        onClick={() => setShowUserMenu(false)}
                      >
                        <User size={16} /> H s c nhn
                      </Link>
                      <button
                        onClick={handleLogout}
                        style={{
                          display: 'flex',
                          alignItems: 'center',
                          gap: 8,
                          padding: 12,
                          width: '100%',
                          background: 'none',
                          border: 'none',
                          color: '#d32f2f',
                          fontWeight: 500,
                          cursor: 'pointer',
                        }}
                      >
                        <LogOut size={16} /> ng xut
                      </button>
                    </div>
                  )}
                </div>
              )}
              <button
                className={styles.menuToggle}
                onClick={() => setIsMenuOpen(!isMenuOpen)}
                aria-label={isMenuOpen ? 'ng menu' : 'M menu'}
              >
                {isMenuOpen ? <X /> : <Menu />}
              </button>
            </div>
          </div>
        </div>
      </header>

      {/* Welcome/Header Section - ng nht vi Dashboard */}
      <section
        className={styles.about}
        style={{ paddingTop: '40px', paddingBottom: '60px' }}
      >
        <div className={styles.container}>
          <div style={{ textAlign: 'center', marginBottom: '40px' }}>
            <h1
              style={{
                fontSize: 'clamp(28px, 4vw, 42px)',
                fontWeight: '700',
                lineHeight: '1.2',
                marginBottom: '16px',
                color: 'var(--text-primary)',
              }}
            >
              Theo di chu k sinh sn
            </h1>
            <p
              style={{
                fontSize: 'clamp(16px, 2vw, 20px)',
                color: 'var(--text-secondary)',
                margin: '0 0 32px 0',
                lineHeight: '1.5',
                fontWeight: '400',
              }}
            >
              Qun l v theo di sc khe sinh sn mt cch thng minh
            </p>

            {predictions.currentPhase !== 'unknown' && (
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center',
                  gap: '16px',
                  padding: '24px 32px',
                  background: currentPhaseInfo.bgColor,
                  borderRadius: 'var(--border-radius-lg)',
                  color: currentPhaseInfo.color,
                  fontSize: '16px',
                  fontWeight: '600',
                  marginBottom: '32px',
                  border: `2px solid ${currentPhaseInfo.color}20`,
                  maxWidth: '600px',
                  margin: '0 auto 32px auto',
                  boxShadow: '0 8px 32px rgba(0, 0, 0, 0.1)',
                }}
              >
                <div
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: '12px',
                    fontSize: '20px',
                    fontWeight: '700',
                  }}
                >
                  <span style={{ fontSize: '24px' }}>
                    {currentPhaseInfo.icon}
                  </span>
                  <span>Giai on hin ti: {currentPhaseInfo.label}</span>
                </div>

                <div
                  style={{
                    textAlign: 'center',
                    fontSize: '16px',
                    fontWeight: '500',
                    color: '#2d3748',
                    lineHeight: '1.5',
                  }}
                >
                  <p style={{ margin: '0 0 8px 0', fontStyle: 'italic' }}>
                    {currentPhaseInfo.description}
                  </p>
                  <p
                    style={{
                      margin: '0',
                      fontSize: '14px',
                      color: '#4a5568',
                      background: 'rgba(255, 255, 255, 0.8)',
                      padding: '8px 16px',
                      borderRadius: '8px',
                      border: '1px solid rgba(255, 255, 255, 0.6)',
                    }}
                  >
                     <strong>Li khuyn:</strong> {currentPhaseInfo.advice}
                  </p>
                </div>
              </div>
            )}

            {/* Header Actions - S dng HomePage button styles */}
            <div
              style={{
                display: 'flex',
                gap: '16px',
                alignItems: 'center',
                flexWrap: 'wrap',
                justifyContent: 'center',
              }}
            >
              <button
                onClick={() => setShowPeriodForm(true)}
                className={styles.btnPrimary}
                style={{ display: 'flex', alignItems: 'center', gap: '8px' }}
              >
                <Droplets size={18} />
                Ghi nhn k kinh
              </button>
              <button
                onClick={() => setShowHistoryModal(true)}
                className={styles.btnOutline}
                style={{ display: 'flex', alignItems: 'center', gap: '8px' }}
              >
                <History size={18} />
                Lch s
              </button>
              <button
                onClick={() => setShowAnalyticsModal(true)}
                className={styles.btnOutline}
                style={{ display: 'flex', alignItems: 'center', gap: '8px' }}
              >
                <TrendingUp size={18} />
                Phn tch
              </button>
              <button
                onClick={() => setShowExportModal(true)}
                className={styles.btnOutline}
                style={{ display: 'flex', alignItems: 'center', gap: '8px' }}
              >
                <TrendingUp size={18} />
                Xut d liu
              </button>
            </div>
          </div>
        </div>
      </section>

      {/* Main Content - ng nht container vi HomePage */}
      <section
        style={{ background: 'var(--background-light)', padding: '60px 0' }}
      >
        <div className={styles.container}>
          {/* Smart Notifications */}
          {notifications.length > 0 && (
            <div className="notifications-section">
              <h3 className="section-title">
                <Bell size={20} />
                Thng bo thng minh
              </h3>
              <div className="notifications-list">
                {notifications.map(notification => (
                  <div
                    key={notification.id}
                    className={`notification-card ${notification.priority}`}
                  >
                    <div className="notification-icon">
                      {notification.type === 'ovulation' && <Heart size={20} />}
                      {notification.type === 'period' && <Droplets size={20} />}
                      {notification.type === 'health' && <Activity size={20} />}
                    </div>
                    <div className="notification-content">
                      <h4>{notification.title}</h4>
                      <p>{notification.message}</p>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          )}

          {/* Overview Cards */}
          <div className="cycle-overview">
            <div className="cycle-card">
              <Calendar className="cycle-icon pink" />
              <div className="cycle-card-info">
                <p>K kinh tip theo</p>
                <p className="cycle-card-value">
                  {getDaysUntilNextPeriod() !== null
                    ? `${getDaysUntilNextPeriod()} ngy`
                    : 'Cha thit lp'}
                </p>
              </div>
            </div>

            <div className="cycle-card">
              <Heart className="cycle-icon orange" />
              <div className="cycle-card-info">
                <p>Rng trng</p>
                <p className="cycle-card-value">
                  {getDaysUntilOvulation() !== null
                    ? `${getDaysUntilOvulation()} ngy`
                    : 'Cha thit lp'}
                </p>
              </div>
            </div>

            <div className="cycle-card">
              <Droplets className="cycle-icon blue" />
              <div className="cycle-card-info">
                <p> di chu k</p>
                <p className="cycle-card-value">{cycleData.cycleLength} ngy</p>
              </div>
            </div>

            <div className="cycle-card">
              <div
                className={`cycle-icon ${
                  isInFertilityWindow() ? 'pink' : 'text-gray-300'
                }`}
              >
                <div
                  style={{
                    width: '12px',
                    height: '12px',
                    borderRadius: '50%',
                    backgroundColor: 'currentColor',
                  }}
                ></div>
              </div>
              <div className="cycle-card-info">
                <p>Thi k mu m</p>
                <p className="cycle-card-value">
                  {isInFertilityWindow() ? 'ang din ra' : 'Khng'}
                </p>
              </div>
            </div>
          </div>

          <div className="cycle-content-grid">
            {/* Calendar */}
            <div className="calendar-section">
              <div className="calendar-header">
                <h2 className="calendar-title">
                  {format(viewingMonth, 'MMMM yyyy')}
                </h2>
                <div className="calendar-nav">
                  <button
                    onClick={() => setViewingMonth(subMonths(viewingMonth, 1))}
                    className="nav-btn"
                  >
                    
                  </button>
                  <button
                    onClick={() => setViewingMonth(new Date())}
                    className="nav-btn today-btn"
                  >
                    Hm nay
                  </button>
                  <button
                    onClick={() => setViewingMonth(addMonths(viewingMonth, 1))}
                    className="nav-btn"
                  >
                    
                  </button>
                </div>
              </div>

              {/* Calendar Grid */}
              <div className="calendar-weekdays">
                {['CN', 'T2', 'T3', 'T4', 'T5', 'T6', 'T7'].map(day => (
                  <div key={day} className="weekday">
                    {day}
                  </div>
                ))}
              </div>

              <div className="calendar-days">
                {monthDays.map(day => {
                  const phase = getPeriodPhase(day);
                  const isToday = isSameDay(day, today);
                  const isSelected = isSameDay(day, selectedDate);
                  const isOtherMonth = !isSameMonth(day, viewingMonth);
                  const dayData = getDataForDate(day);
                  const hasData =
                    dayData.symptoms.length > 0 ||
                    dayData.mood.length > 0 ||
                    dayData.notes.length > 0;

                  let dayClass = 'calendar-day';
                  if (isToday) dayClass += ' today';
                  if (isSelected) dayClass += ' selected';
                  if (isOtherMonth) dayClass += ' other-month';
                  if (phase) dayClass += ` ${phase}`;
                  if (hasData) dayClass += ' has-data';

                  return (
                    <div
                      key={day.toString()}
                      className={dayClass}
                      onClick={() => setSelectedDate(day)}
                    >
                      <span className="day-number">{format(day, 'd')}</span>
                      {hasData && <div className="data-indicator"></div>}
                    </div>
                  );
                })}
              </div>

              {/* Legend */}
              <div className="calendar-legend">
                <div className="legend-item">
                  <div className="legend-color period"></div>
                  <span>K kinh</span>
                </div>
                <div className="legend-item">
                  <div className="legend-color ovulation"></div>
                  <span>Rng trng</span>
                </div>
                <div className="legend-item">
                  <div className="legend-color fertility"></div>
                  <span>Thi k mu m</span>
                </div>
                <div className="legend-item">
                  <div className="legend-color luteal"></div>
                  <span>Hong th</span>
                </div>
              </div>
            </div>

            {/* Side Panel */}
            <div className="side-panel">
              {/* Selected Date Info */}
              <div className="panel-card">
                <h3 className="panel-title">
                  <Info size={18} />
                  {format(selectedDate, 'dd/MM/yyyy')}
                </h3>

                {selectedDateData.symptoms.length > 0 && (
                  <div className="date-data-section">
                    <h4>Triu chng:</h4>
                    {selectedDateData.symptoms.map((item, index) => (
                      <span key={index} className="data-tag symptom">
                        {item.symptom}
                      </span>
                    ))}
                  </div>
                )}

                {selectedDateData.mood.length > 0 && (
                  <div className="date-data-section">
                    <h4>Tm trng:</h4>
                    {selectedDateData.mood.map((item, index) => {
                      const mood = moodIcons[item.mood];
                      return (
                        <span key={index} className="data-tag mood">
                          <mood.icon size={16} />
                          {mood.label}
                        </span>
                      );
                    })}
                  </div>
                )}

                {selectedDateData.notes.length > 0 && (
                  <div className="date-data-section">
                    <h4>Ghi ch:</h4>
                    {selectedDateData.notes.map((item, index) => (
                      <p key={index} className="note-text">
                        {item.note}
                      </p>
                    ))}
                  </div>
                )}
              </div>

              {/* Quick Actions */}
              <div className="panel-card">
                <h3 className="panel-title">Ghi nhn nhanh</h3>
                <div className="quick-actions">
                  <button
                    onClick={() => {
                      setModalType('symptom');
                      setShowAddModal(true);
                    }}
                    className="quick-action-btn blue"
                  >
                    <Plus className="quick-action-icon" />
                    <span>Thm triu chng</span>
                  </button>

                  <button
                    onClick={() => {
                      setModalType('mood');
                      setShowAddModal(true);
                    }}
                    className="quick-action-btn green"
                  >
                    <Plus className="quick-action-icon" />
                    <span>Ghi nhn tm trng</span>
                  </button>

                  <button
                    onClick={() => {
                      setModalType('note');
                      setShowAddModal(true);
                    }}
                    className="quick-action-btn purple"
                  >
                    <Plus className="quick-action-icon" />
                    <span>Thm ghi ch</span>
                  </button>
                </div>
              </div>

              {/* Cycle Insights */}
              <div className="panel-card">
                <CycleInsights
                  cycleData={cycleData}
                  predictions={predictions}
                />
              </div>

              {/* Health Recommendations */}
              {healthRecommendations.length > 0 && (
                <div className="panel-card">
                  <h3 className="panel-title">
                    <BookOpen size={18} />
                    Li khuyn cho bn
                  </h3>
                  <div className="recommendations-list">
                    {healthRecommendations.map((rec, index) => (
                      <div key={index} className="recommendation-item">
                        <div className="rec-icon">
                          {rec.type === 'rest' && <Moon size={16} />}
                          {rec.type === 'food' && <Sun size={16} />}
                          {rec.type === 'exercise' && <Activity size={16} />}
                          {rec.type === 'nutrition' && <Sparkles size={16} />}
                          {rec.type === 'hydration' && <Droplets size={16} />}
                          {rec.type === 'monitoring' && (
                            <TrendingUp size={16} />
                          )}
                          {rec.type === 'mood' && <Heart size={16} />}
                          {rec.type === 'preparation' && <Calendar size={16} />}
                          {rec.type === 'comfort' && <Smile size={16} />}
                        </div>
                        <p>{rec.text}</p>
                      </div>
                    ))}
                  </div>
                </div>
              )}

              {/* Quick Services */}
              <div className="panel-card">
                <h3 className="panel-title">Dch v lin quan</h3>
                <div className="services-list">
                  <Link to="/consultation" className="service-link">
                    <MessageCircle size={16} />
                    <span>T vn vi chuyn gia</span>
                    <ChevronRight size={16} />
                  </Link>
                  <Link to="/sti-testing" className="service-link">
                    <TestTube size={16} />
                    <span>Xt nghim STI</span>
                    <ChevronRight size={16} />
                  </Link>
                  <Link to="/qa" className="service-link">
                    <BookOpen size={16} />
                    <span>Hi p sc khe</span>
                    <ChevronRight size={16} />
                  </Link>
                </div>
              </div>

              {/* Settings */}
              <div className="panel-card">
                <h3 className="panel-title">
                  <Settings size={18} />
                  Thit lp chu k
                </h3>
                <div className="settings-form">
                  <div className="form-group">
                    <label className="form-label"> di chu k (ngy)</label>
                    <input
                      type="number"
                      value={cycleData.cycleLength}
                      onChange={e =>
                        updateCycleData({
                          cycleLength: parseInt(e.target.value),
                        })
                      }
                      className="form-input"
                      min="21"
                      max="35"
                    />
                  </div>

                  <div className="form-group">
                    <label className="form-label">S ngy kinh (ngy)</label>
                    <input
                      type="number"
                      value={cycleData.periodLength}
                      onChange={e =>
                        updateCycleData({
                          periodLength: parseInt(e.target.value),
                        })
                      }
                      className="form-input"
                      min="3"
                      max="7"
                    />
                  </div>

                  <button
                    onClick={() => setShowSettingsModal(true)}
                    className="advanced-settings-btn"
                  >
                    <Settings size={16} />
                    Ci t nng cao
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Period Start Form Modal */}
      {showPeriodForm && (
        <div className="modal-overlay">
          <div className="modal-content">
            <h3 className="modal-title">Ghi nhn k kinh mi</h3>

            <div className="period-form">
              <div className="form-group">
                <label className="form-label">Ngy bt u</label>
                <input
                  type="date"
                  value={periodFormData.startDate}
                  onChange={e =>
                    setPeriodFormData(prev => ({
                      ...prev,
                      startDate: e.target.value,
                    }))
                  }
                  className="form-input"
                />
              </div>

              <div className="form-group">
                <label className="form-label">D kin s ngy kinh</label>
                <input
                  type="number"
                  value={periodFormData.expectedLength}
                  onChange={e =>
                    setPeriodFormData(prev => ({
                      ...prev,
                      expectedLength: e.target.value,
                    }))
                  }
                  className="form-input"
                  min="3"
                  max="7"
                />
              </div>
            </div>

            <div className="modal-actions">
              <button
                onClick={() => setShowPeriodForm(false)}
                className="modal-btn cancel"
              >
                Hy
              </button>
              <button onClick={handleStartPeriod} className="modal-btn confirm">
                Lu thng tin
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Add Data Modal */}
      {showAddModal && (
        <div className="modal-overlay">
          <div className="modal-content">
            <h3 className="modal-title">
              {modalType === 'symptom' && 'Thm triu chng'}
              {modalType === 'mood' && 'Ghi nhn tm trng'}
              {modalType === 'note' && 'Thm ghi ch'}
            </h3>

            {modalType === 'symptom' && (
              <div className="option-list">
                {symptomList.map(symptom => (
                  <button
                    key={symptom}
                    onClick={() => {
                      addSymptom(format(selectedDate, 'yyyy-MM-dd'), symptom);
                      setShowAddModal(false);
                    }}
                    className="option-item"
                  >
                    {symptom}
                  </button>
                ))}
              </div>
            )}

            {modalType === 'mood' && (
              <div className="option-list">
                {Object.entries(moodIcons).map(([key, mood]) => (
                  <button
                    key={key}
                    onClick={() => {
                      addMood(format(selectedDate, 'yyyy-MM-dd'), key);
                      setShowAddModal(false);
                    }}
                    className="mood-item"
                  >
                    <mood.icon className={`mood-icon ${mood.color}`} />
                    {mood.label}
                  </button>
                ))}
              </div>
            )}

            {modalType === 'note' && (
              <div>
                <textarea
                  placeholder="Nhp ghi ch ca bn..."
                  className="note-input"
                  rows="3"
                  value={newNoteText}
                  onChange={e => setNewNoteText(e.target.value)}
                />
                <button
                  onClick={handleAddNote}
                  className="modal-btn confirm"
                  disabled={!newNoteText.trim()}
                >
                  Lu ghi ch
                </button>
              </div>
            )}

            <div className="modal-actions">
              <button
                onClick={() => setShowAddModal(false)}
                className="modal-btn cancel"
              >
                Hy
              </button>
            </div>
          </div>
        </div>
      )}

      {/* History Modal */}
      {showHistoryModal && (
        <div className="modal-overlay">
          <div className="modal-content large">
            <h3 className="modal-title">
              <History size={20} />
              Lch s chu k
            </h3>

            <div className="history-content">
              {cycleData.cycleHistory && cycleData.cycleHistory.length > 0 ? (
                <div className="history-list">
                  {cycleData.cycleHistory.map(cycle => (
                    <div key={cycle.id} className="history-item">
                      <div className="history-dates">
                        <strong>
                          {format(new Date(cycle.startDate), 'dd/MM/yyyy')}
                        </strong>
                        <span> - </span>
                        <strong>
                          {format(new Date(cycle.endDate), 'dd/MM/yyyy')}
                        </strong>
                      </div>
                      <div className="history-info">
                        <span> di: {cycle.cycleLength} ngy</span>
                        {cycle.symptoms.length > 0 && (
                          <span>Triu chng: {cycle.symptoms.join(', ')}</span>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <div className="no-history">
                  <p>
                    Cha c lch s chu k. Hy tip tc theo di  xy dng d
                    liu!
                  </p>
                </div>
              )}
            </div>

            <div className="modal-actions">
              <button
                onClick={() => setShowHistoryModal(false)}
                className="modal-btn cancel"
              >
                ng
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Analytics Modal */}
      {showAnalyticsModal && (
        <div className="modal-overlay">
          <div className="modal-content large">
            <h3 className="modal-title">
              <TrendingUp size={20} />
              Phn tch chu k
            </h3>

            <div className="analytics-content">
              {analytics ? (
                <div className="analytics-stats">
                  <div className="stat-grid">
                    <div className="stat-item">
                      <h4>Tng s chu k</h4>
                      <p className="stat-value">{analytics.totalCycles}</p>
                    </div>
                    <div className="stat-item">
                      <h4> di trung bnh</h4>
                      <p className="stat-value">{analytics.avgLength} ngy</p>
                    </div>
                    <div className="stat-item">
                      <h4>Chu k ngn nht</h4>
                      <p className="stat-value">{analytics.minLength} ngy</p>
                    </div>
                    <div className="stat-item">
                      <h4>Chu k di nht</h4>
                      <p className="stat-value">{analytics.maxLength} ngy</p>
                    </div>
                    <div className="stat-item">
                      <h4> u n</h4>
                      <p className="stat-value">{analytics.regularity}</p>
                    </div>
                    <div className="stat-item">
                      <h4> lch chun</h4>
                      <p className="stat-value">{analytics.variance}</p>
                    </div>
                  </div>

                  {notificationPermission !== 'granted' && (
                    <div
                      style={{
                        marginTop: '20px',
                        padding: '16px',
                        background: '#fff3cd',
                        borderRadius: '8px',
                      }}
                    >
                      <h4>Bt thng bo  nhn nhc nh</h4>
                      <p>
                        Cho php thng bo  nhn nhc nh v chu k sinh sn
                      </p>
                      <button
                        onClick={handleEnableNotifications}
                        className="modal-btn confirm"
                      >
                        <Bell size={16} />
                        Bt thng bo
                      </button>
                    </div>
                  )}
                </div>
              ) : (
                <div className="no-analytics">
                  <p>
                    Cn t nht 2 chu k  thc hin phn tch. Hy tip tc
                    theo di!
                  </p>
                </div>
              )}
            </div>

            <div className="modal-actions">
              <button
                onClick={() => setShowAnalyticsModal(false)}
                className="modal-btn cancel"
              >
                ng
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Export Modal */}
      {showExportModal && (
        <div className="modal-overlay">
          <div className="modal-content">
            <h3 className="modal-title">
              <TrendingUp size={20} />
              Xut d liu
            </h3>

            <div className="export-content">
              <p>
                Xut ton b d liu chu k sinh sn ca bn  sao lu hoc
                chia s vi bc s.
              </p>

              <div className="export-info">
                <h4>D liu s bao gm:</h4>
                <ul>
                  <li>Thng tin chu k kinh nguyt</li>
                  <li>Lch s cc chu k</li>
                  <li>Triu chng v tm trng</li>
                  <li>Ghi ch c nhn</li>
                  <li>D on v phn tch</li>
                </ul>
              </div>

              {safeMode && (
                <div
                  style={{
                    padding: '12px',
                    background: '#fff3cd',
                    borderRadius: '8px',
                    marginTop: '16px',
                  }}
                >
                  <p style={{ margin: 0, fontSize: '14px', color: '#856404' }}>
                     D liu ang c ti. Vui lng i mt cht  xut y
                    .
                  </p>
                </div>
              )}
            </div>

            <div className="modal-actions">
              <button
                onClick={() => setShowExportModal(false)}
                className="modal-btn cancel"
              >
                Hy
              </button>
              <button
                onClick={() => {
                  handleExportData();
                  setShowExportModal(false);
                }}
                className="modal-btn confirm"
                disabled={safeMode}
              >
                Xut d liu JSON
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Settings Modal */}
      {showSettingsModal && (
        <div className="modal-overlay">
          <div className="modal-content">
            <h3 className="modal-title">
              <Settings size={20} />
              Ci t nng cao
            </h3>

            <div className="settings-content">
              <div className="setting-group">
                <h4>Thng bo</h4>
                <div className="setting-item">
                  <label>
                    <input
                      type="checkbox"
                      checked={notificationPermission === 'granted'}
                      onChange={handleEnableNotifications}
                    />
                    Bt thng bo trnh duyt
                  </label>
                </div>
              </div>

              <div className="setting-group">
                <h4>Chu k mc nh</h4>
                <div className="setting-item">
                  <label> di chu k trung bnh (ngy)</label>
                  <input
                    type="number"
                    value={cycleData.cycleLength}
                    onChange={e =>
                      updateCycleData({ cycleLength: parseInt(e.target.value) })
                    }
                    min="21"
                    max="35"
                  />
                </div>
                <div className="setting-item">
                  <label>S ngy kinh trung bnh</label>
                  <input
                    type="number"
                    value={cycleData.periodLength}
                    onChange={e =>
                      updateCycleData({
                        periodLength: parseInt(e.target.value),
                      })
                    }
                    min="3"
                    max="7"
                  />
                </div>
              </div>
            </div>

            <div className="modal-actions">
              <button
                onClick={() => setShowSettingsModal(false)}
                className="modal-btn cancel"
              >
                ng
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default CycleTracking;
