const HomePageRoutes = [
  // Main pages
  {
    path: '/',
    element: <HomePage />,
  },
  {
    path: '/search',
    element: <SearchPage />,
  },

  // Service pages with Vietnamese URLs (bc ProtectedRoute + MainLayout)
  {
    path: '/tu-van',
    element: (
      <ProtectedRoute>
        <MainLayout title="T vn trc tuyn - Gynexa">
          <Consultation />
        </MainLayout>
      </ProtectedRoute>
    ),
  },
  {
    path: '/theo-doi-chu-ky',
    element: (
      <ProtectedRoute>
        <MainLayout title="Theo di chu k - Gynexa">
          <CycleTracking />
        </MainLayout>
      </ProtectedRoute>
    ),
  },
  {
    path: '/xet-nghiem-sti',
    element: (
      <ProtectedRoute>
        <MainLayout title="Xt nghim STIs - Gynexa">
          <STITesting />
        </MainLayout>
      </ProtectedRoute>
    ),
  },
  {
    path: '/hoi-dap',
    element: (
      <ProtectedRoute>
        <MainLayout title="Hi p - Gynexa">
          <QA />
        </MainLayout>
      </ProtectedRoute>
    ),
  },
  {
    path: '/suc-khoe',
    element: <HealthCarePage />,
  },

  // English URLs for backward compatibility
  {
    path: '/consultation',
    element: <Consultation />,
  },
  {
    path: '/cycle-tracking',
    element: <CycleTracking />,
  },
  {
    path: '/sti-testing',
    element: <STITesting />,
  },
  {
    path: '/qa',
    element: <QA />,
  },

  // Information pages
  {
    path: '/about',
    element: <AboutPage />,
  },
  {
    path: '/gioi-thieu',
    element: <AboutPage />,
  },
  {
    path: '/services',
    element: <ServicesPage />,
  },
  {
    path: '/dich-vu',
    element: <ServicesPage />,
  },
  {
    path: '/doctors',
    element: <DoctorsPage />,
  },
  {
    path: '/doi-ngu',
    element: <DoctorsPage />,
  },
  {
    path: '/blog',
    element: <BlogPage />,
  },
  {
    path: '/blog/:id',
    element: <BlogPage />,
  },
  {
    path: '/tin-tuc',
    element: <BlogPage />,
  },
  {
    path: '/contact',
    element: <ContactPage />,
  },
  {
    path: '/lien-he',
    element: <ContactPage />,
  },

  // Legal pages
  {
    path: '/terms',
    element: <TermsPage />,
  },
  {
    path: '/dieu-khoan',
    element: <TermsPage />,
  },
  {
    path: '/privacy',
    element: <PrivacyPage />,
  },
  {
    path: '/chinh-sach-bao-mat',
    element: <PrivacyPage />,
  },
];

export default HomePageRoutes;
