import React from 'react';
import {
  TrendingUp,
  AlertTriangle,
  CheckCircle,
  Heart,
  Calendar,
} from 'lucide-react';
import { differenceInDays, format, addDays } from 'date-fns';

const CycleInsights = ({ cycleData, predictions }) => {
  const generateInsights = () => {
    const insights = [];

    if (!cycleData.lastPeriod) {
      insights.push({
        type: 'info',
        icon: Calendar,
        title: 'Bt u theo di',
        message:
          'Hy ghi nhn ngy bt u k kinh  nhn c d on chnh xc.',
        color: '#3a99b7',
      });
      return insights;
    }

    const today = new Date();
    const lastPeriod = new Date(cycleData.lastPeriod);
    const daysSinceLastPeriod = differenceInDays(today, lastPeriod);
    const cycleDay =
      ((daysSinceLastPeriod % cycleData.cycleLength) + cycleData.cycleLength) %
      cycleData.cycleLength;

    // Insight v giai on hin ti
    if (predictions.currentPhase === 'menstrual') {
      insights.push({
        type: 'current',
        icon: CheckCircle,
        title: 'ang trong k kinh',
        message: 'Hy ch  ngh ngi y  v b sung st trong ch  n.',
        color: '#e91e63',
      });
    } else if (predictions.currentPhase === 'fertile') {
      insights.push({
        type: 'fertility',
        icon: Heart,
        title: 'Thi k mu m',
        message: 'y l thi im c kh nng th thai cao nht trong chu k.',
        color: '#ff9800',
      });
    } else if (predictions.currentPhase === 'ovulation') {
      insights.push({
        type: 'ovulation',
        icon: Heart,
        title: 'Ngy rng trng',
        message:
          'Hm nay l ngy d on rng trng. Theo di du hiu c th!',
        color: '#ff5722',
      });
    }

    // Insight v chu k sp ti
    if (predictions.nextPeriod) {
      const daysToNextPeriod = differenceInDays(predictions.nextPeriod, today);

      if (daysToNextPeriod <= 3 && daysToNextPeriod >= 0) {
        insights.push({
          type: 'upcoming',
          icon: Calendar,
          title: 'K kinh sp ti',
          message: `K kinh d kin bt u trong ${daysToNextPeriod} ngy. Hy chun b sn sng!`,
          color: '#9c27b0',
        });
      }
    }

    // Phn tch chu k
    if (cycleData.cycleHistory && cycleData.cycleHistory.length >= 3) {
      const recentCycles = cycleData.cycleHistory.slice(-3);
      const cycleLengths = recentCycles.map(cycle => cycle.cycleLength);
      const avgLength =
        cycleLengths.reduce((sum, length) => sum + length, 0) /
        cycleLengths.length;
      const variance =
        cycleLengths.reduce(
          (sum, length) => sum + Math.pow(length - avgLength, 2),
          0
        ) / cycleLengths.length;

      if (variance > 9) {
        insights.push({
          type: 'warning',
          icon: AlertTriangle,
          title: 'Chu k khng u',
          message:
            'Chu k gn y c s thay i ln. Hy tham kho  kin chuyn gia.',
          color: '#f44336',
        });
      } else if (variance < 4) {
        insights.push({
          type: 'positive',
          icon: CheckCircle,
          title: 'Chu k u n',
          message:
            'Chu k ca bn rt u n! Tip tc duy tr li sng lnh mnh.',
          color: '#4caf50',
        });
      }
    }

    // Phn tch triu chng
    if (cycleData.symptoms) {
      const recentSymptoms = Object.values(cycleData.symptoms)
        .flat()
        .filter(symptom => {
          const symptomDate = new Date(symptom.time);
          return differenceInDays(today, symptomDate) <= 30;
        });

      const symptomCounts = {};
      recentSymptoms.forEach(symptom => {
        symptomCounts[symptom.symptom] =
          (symptomCounts[symptom.symptom] || 0) + 1;
      });

      const mostCommonSymptom = Object.entries(symptomCounts).sort(
        ([, a], [, b]) => b - a
      )[0];

      if (mostCommonSymptom && mostCommonSymptom[1] >= 3) {
        insights.push({
          type: 'pattern',
          icon: TrendingUp,
          title: 'Triu chng thng gp',
          message: `Bn thng gp "${mostCommonSymptom[0]}" trong thng qua. Hy ghi ch  theo di xu hng.`,
          color: '#607d8b',
        });
      }

      // Cnh bo triu chng nghim trng
      const seriousSymptoms = recentSymptoms.filter(symptom =>
        ['Chy mu bt thng', 'au bng d di', 'St cao'].includes(
          symptom.symptom
        )
      );

      if (seriousSymptoms.length > 0) {
        insights.push({
          type: 'urgent',
          icon: AlertTriangle,
          title: 'Cn ch ',
          message:
            'Bn c triu chng cn c t vn y t. Hy lin h vi chuyn gia.',
          color: '#f44336',
        });
      }
    }

    return insights;
  };

  const insights = generateInsights();

  if (insights.length === 0) {
    return null;
  }

  return (
    <div className="cycle-insights">
      <h3 className="insights-title">
        <TrendingUp size={20} />
        Nhn nh thng minh
      </h3>

      <div className="insights-list">
        {insights.map((insight, index) => (
          <div key={index} className={`insight-item ${insight.type}`}>
            <div className="insight-icon" style={{ color: insight.color }}>
              <insight.icon size={24} />
            </div>
            <div className="insight-content">
              <h4 className="insight-title">{insight.title}</h4>
              <p className="insight-message">{insight.message}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default CycleInsights;
