import React from 'react';
import {
  Heart,
  Send,
  Phone,
  Mail,
  MapPin,
  Facebook,
  Twitter,
  Instagram,
  Youtube,
} from 'lucide-react';
import { Link } from 'react-router-dom';
import styles from './Footer.module.css';

const Footer = () => {
  const handleNewsletterSubmit = e => {
    e.preventDefault();
    const email = e.target.email.value;
    // TODO: Integrate with backend API for newsletter subscription
    console.log('Newsletter subscription for:', email);
    alert('ng k newsletter thnh cng! Cm n bn  quan tm.');
    e.target.reset();
  };

  const companyLinks = [
    { name: 'V chng ti', path: '/gioi-thieu' },
    { name: 'Dch v', path: '/dich-vu' },
    { name: 'i ng y t', path: '/doi-ngu' },
    { name: 'Tin tc', path: '/tin-tuc' },
    { name: 'Lin h', path: '/lien-he' },
  ];

  const serviceLinks = [
    { name: 'T vn trc tuyn', path: '/tu-van' },
    { name: 'Theo di chu k', path: '/theo-doi-chu-ky' },
    { name: 'Xt nghim STIs', path: '/xet-nghiem-sti' },
    { name: 'Hi p', path: '/hoi-dap' },
    { name: 'Chm sc sc khe', path: '/suc-khoe' },
  ];

  const legalLinks = [
    { name: 'iu khon s dng', path: '/dieu-khoan' },
    { name: 'Chnh sch bo mt', path: '/chinh-sach-bao-mat' },
    { name: 'ng nhp', path: '/login' },
    { name: 'ng k', path: '/register' },
  ];

  const socialLinks = [
    { name: 'Facebook', icon: Facebook, url: 'https://facebook.com/gynexa' },
    { name: 'Instagram', icon: Instagram, url: 'https://instagram.com/gynexa' },
    { name: 'Twitter', icon: Twitter, url: 'https://twitter.com/gynexa' },
    { name: 'Youtube', icon: Youtube, url: 'https://youtube.com/gynexa' },
  ];

  return (
    <footer className={styles.footer}>
      <div className={styles.container}>
        {/* Main Footer Content */}
        <div className={styles.footerContent}>
          {/* Company Info */}
          <div className={styles.footerSection}>
            <Link to="/" className={styles.footerLogo}>
              <Heart className={styles.logoIcon} />
              <span className={styles.logoText}>GYNEXA</span>
            </Link>
            <p className={styles.companyDescription}>
              Bt nhp c th, hiu thu chnh mnh.
              <br />
              Nn tng chm sc sc khe gii tnh hng u Vit Nam.
            </p>

            {/* Contact Info */}
            <div className={styles.contactInfo}>
              <div className={styles.contactItem}>
                <Phone size={16} />
                <span>(84) 123-456-789</span>
              </div>
              <div className={styles.contactItem}>
                <Mail size={16} />
                <span>support@gynexa.com</span>
              </div>
              <div className={styles.contactItem}>
                <MapPin size={16} />
                <span>123 Nguyn Hu, Qun 1, TP.HCM</span>
              </div>
            </div>

            {/* Social Media */}
            <div className={styles.socialMedia}>
              <h4>Kt ni vi chng ti</h4>
              <div className={styles.socialLinks}>
                {socialLinks.map((social, index) => {
                  const IconComponent = social.icon;
                  return (
                    <a
                      key={index}
                      href={social.url}
                      target="_blank"
                      rel="noopener noreferrer"
                      className={styles.socialLink}
                      aria-label={social.name}
                    >
                      <IconComponent size={20} />
                    </a>
                  );
                })}
              </div>
            </div>
          </div>

          {/* Company Links */}
          <div className={styles.footerSection}>
            <h4>Cng ty</h4>
            <ul className={styles.footerLinks}>
              {companyLinks.map((link, index) => (
                <li key={index}>
                  <Link to={link.path} className={styles.footerLink}>
                    {link.name}
                  </Link>
                </li>
              ))}
            </ul>
          </div>

          {/* Services */}
          <div className={styles.footerSection}>
            <h4>Dch v</h4>
            <ul className={styles.footerLinks}>
              {serviceLinks.map((link, index) => (
                <li key={index}>
                  <Link to={link.path} className={styles.footerLink}>
                    {link.name}
                  </Link>
                </li>
              ))}
            </ul>
          </div>

          {/* Quick Access & Newsletter */}
          <div className={styles.footerSection}>
            <h4>Truy cp nhanh</h4>
            <ul className={styles.footerLinks}>
              {legalLinks.map((link, index) => (
                <li key={index}>
                  <Link to={link.path} className={styles.footerLink}>
                    {link.name}
                  </Link>
                </li>
              ))}
            </ul>

            {/* Newsletter */}
            <div className={styles.newsletter}>
              <h4>ng k nhn tin</h4>
              <p>Nhn thng tin v sc khe v u i mi nht</p>
              <form
                onSubmit={handleNewsletterSubmit}
                className={styles.newsletterForm}
              >
                <div className={styles.newsletterInput}>
                  <input
                    type="email"
                    name="email"
                    placeholder="Nhp email ca bn"
                    required
                  />
                  <button type="submit" aria-label="ng k">
                    <Send size={16} />
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>

        {/* Footer Bottom */}
        <div className={styles.footerBottom}>
          <div className={styles.footerBottomContent}>
            <div className={styles.copyright}>
              <p>&copy; 2024 Gynexa Healthcare. Tt c quyn c bo lu.</p>
            </div>
            <div className={styles.footerBottomLinks}>
              <Link to="/dieu-khoan" className={styles.bottomLink}>
                iu khon
              </Link>
              <Link to="/chinh-sach-bao-mat" className={styles.bottomLink}>
                Bo mt
              </Link>
              <Link to="/lien-he" className={styles.bottomLink}>
                Lin h
              </Link>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
