import React, { useState, useEffect } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  IconButton,
  Chip,
  Avatar,
  Box,
  Typography,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Pagination,
} from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import VisibilityIcon from '@mui/icons-material/Visibility';
import ImageIcon from '@mui/icons-material/Image';
import ArticleIcon from '@mui/icons-material/Article';
import VideoLibraryIcon from '@mui/icons-material/VideoLibrary';
import AnnouncementIcon from '@mui/icons-material/Announcement';

const ContentTable = ({ searchTerm }) => {
  const [contents, setContents] = useState([]);
  const [filteredContents, setFilteredContents] = useState([]);
  const [openDialog, setOpenDialog] = useState(false);
  const [editingContent, setEditingContent] = useState(null);
  const [page, setPage] = useState(1);
  const [formData, setFormData] = useState({
    title: '',
    content: '',
    type: 'article',
    status: 'draft',
  });

  const pageSize = 10;

  // Mock data for content management
  const mockContents = [
    {
      id: 1,
      title: 'Hng dn xt nghim mu',
      content: 'Ni dung hng dn chi tit v quy trnh xt nghim mu bao gm chun b trc xt nghim, quy trnh thc hin v c kt qu...',
      type: 'article',
      status: 'published',
      createdAt: '2024-01-15',
      updatedAt: '2024-01-20',
      views: 1250,
      thumbnail: 'https://via.placeholder.com/60x60'
    },
    {
      id: 2,
      title: 'Video: Cch chun b trc xt nghim',
      content: 'Video hng dn cch chun b trc khi i xt nghim, bao gm vic nhn n, ung nc v cc lu  quan trng...',
      type: 'video',
      status: 'published',
      createdAt: '2024-01-10',
      updatedAt: '2024-01-18',
      views: 890,
      thumbnail: 'https://via.placeholder.com/60x60'
    },
    {
      id: 3,
      title: 'Infographic: Cc loi xt nghim ph bin',
      content: 'Hnh nh minh ha cc loi xt nghim thng gp trong chm sc sc khe sinh sn v gii tnh...',
      type: 'image',
      status: 'draft',
      createdAt: '2024-01-12',
      updatedAt: '2024-01-22',
      views: 456,
      thumbnail: 'https://via.placeholder.com/60x60'
    },
    {
      id: 4,
      title: 'Thng bo: Lch ngh l 30/4',
      content: 'Thng bo lch ngh l v iu chnh lch lm vic trong dp l 30/4 v 1/5...',
      type: 'announcement',
      status: 'published',
      createdAt: '2024-01-08',
      updatedAt: '2024-01-25',
      views: 2100,
      thumbnail: 'https://via.placeholder.com/60x60'
    },
    {
      id: 5,
      title: 'FAQ: Cu hi thng gp v xt nghim',
      content: 'Tng hp cc cu hi thng gp v cu tr li chi tit v cc dch v xt nghim...',
      type: 'article',
      status: 'review',
      createdAt: '2024-01-05',
      updatedAt: '2024-01-28',
      views: 678,
      thumbnail: 'https://via.placeholder.com/60x60'
    },
    {
      id: 6,
      title: 'Tm quan trng ca xt nghim nh k',
      content: 'Bi vit v tm quan trng ca vic xt nghim nh k trong vic pht hin sm cc bnh l...',
      type: 'article',
      status: 'published',
      createdAt: '2024-01-03',
      updatedAt: '2024-01-30',
      views: 1456,
      thumbnail: 'https://via.placeholder.com/60x60'
    },
    {
      id: 7,
      title: 'Hng dn c kt qu xt nghim',
      content: 'Video hng dn cch c v hiu cc ch s trong kt qu xt nghim...',
      type: 'video',
      status: 'draft',
      createdAt: '2024-01-01',
      updatedAt: '2024-02-01',
      views: 234,
      thumbnail: 'https://via.placeholder.com/60x60'
    }
  ];

  useEffect(() => {
    setContents(mockContents);
    setFilteredContents(mockContents);
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    const filtered = contents.filter(content =>
      content.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
      content.content.toLowerCase().includes(searchTerm.toLowerCase())
    );
    setFilteredContents(filtered);
    setPage(1); // Reset to first page when search changes
  }, [searchTerm, contents]);

  const getStatusColor = (status) => {
    switch (status) {
      case 'published': return 'success';
      case 'draft': return 'warning';
      case 'review': return 'info';
      case 'archived': return 'default';
      default: return 'default';
    }
  };

  const getStatusText = (status) => {
    switch (status) {
      case 'published': return ' xut bn';
      case 'draft': return 'Bn nhp';
      case 'review': return 'ang duyt';
      case 'archived': return 'Lu tr';
      default: return status;
    }
  };

  const getTypeIcon = (type) => {
    switch (type) {
      case 'article': return <ArticleIcon />;
      case 'video': return <VideoLibraryIcon />;
      case 'image': return <ImageIcon />;
      case 'announcement': return <AnnouncementIcon />;
      default: return <ArticleIcon />;
    }
  };

  const getTypeText = (type) => {
    switch (type) {
      case 'article': return 'Bi vit';
      case 'video': return 'Video';
      case 'image': return 'Hnh nh';
      case 'announcement': return 'Thng bo';
      default: return type;
    }
  };

  const handleEdit = (content) => {
    setEditingContent(content);
    setFormData({
      title: content.title,
      content: content.content,
      type: content.type,
      status: content.status,
    });
    setOpenDialog(true);
  };

  const handleDelete = (id) => {
    if (window.confirm('Bn c chc chn mun xa ni dung ny?')) {
      setContents(contents.filter(content => content.id !== id));
    }
  };

  const handleView = (id) => {
    console.log('Xem chi tit ni dung ID:', id);
    // TODO: Implement view details functionality
  };

  const handleSave = () => {
    if (editingContent) {
      // Update existing content
      setContents(contents.map(content =>
        content.id === editingContent.id
          ? { ...content, ...formData, updatedAt: new Date().toISOString().split('T')[0] }
          : content
      ));
    } else {
      // Add new content
      const newContent = {
        id: Date.now(),
        ...formData,
        createdAt: new Date().toISOString().split('T')[0],
        updatedAt: new Date().toISOString().split('T')[0],
        views: 0,
        thumbnail: 'https://via.placeholder.com/60x60'
      };
      setContents([newContent, ...contents]);
    }
    setOpenDialog(false);
    setEditingContent(null);
    setFormData({
      title: '',
      content: '',
      type: 'article',
      status: 'draft',
    });
  };

  const paginatedData = filteredContents.slice(
    (page - 1) * pageSize,
    page * pageSize
  );

  const totalPages = Math.ceil(filteredContents.length / pageSize);

  return (
    <Box>
      <TableContainer component={Paper} sx={{ borderRadius: '12px', overflow: 'hidden', maxHeight: 600 }}>
        <Table stickyHeader>
          <TableHead>
            <TableRow>
              <TableCell sx={{ bgcolor: '#607d8b', color: 'white', fontWeight: 'bold', minWidth: 60 }}>STT</TableCell>
              <TableCell sx={{ bgcolor: '#607d8b', color: 'white', fontWeight: 'bold', minWidth: 80 }}>Thumbnail</TableCell>
              <TableCell sx={{ bgcolor: '#607d8b', color: 'white', fontWeight: 'bold', minWidth: 250 }}>Tiu </TableCell>
              <TableCell sx={{ bgcolor: '#607d8b', color: 'white', fontWeight: 'bold', minWidth: 120 }}>Loi</TableCell>
              <TableCell sx={{ bgcolor: '#607d8b', color: 'white', fontWeight: 'bold', minWidth: 120 }}>Trng thi</TableCell>
              <TableCell sx={{ bgcolor: '#607d8b', color: 'white', fontWeight: 'bold', minWidth: 100 }}>Lt xem</TableCell>
              <TableCell sx={{ bgcolor: '#607d8b', color: 'white', fontWeight: 'bold', minWidth: 100 }}>Cp nht</TableCell>
              <TableCell sx={{ bgcolor: '#607d8b', color: 'white', fontWeight: 'bold', minWidth: 120 }}>Thao tc</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {paginatedData.map((content, index) => (
              <TableRow 
                key={content.id} 
                hover
                sx={{ '&:hover': { backgroundColor: '#f5f5f5' } }}
              >
                <TableCell>{(page - 1) * pageSize + index + 1}</TableCell>
                <TableCell>
                  <Avatar
                    src={content.thumbnail}
                    sx={{ width: 40, height: 40 }}
                    variant="rounded"
                  >
                    {getTypeIcon(content.type)}
                  </Avatar>
                </TableCell>
                <TableCell>
                  <Box>
                    <Typography variant="subtitle2" sx={{ fontWeight: 500, mb: 0.5 }}>
                      {content.title}
                    </Typography>
                    <Typography 
                      variant="caption" 
                      color="text.secondary"
                      sx={{ 
                        display: 'block',
                        overflow: 'hidden',
                        textOverflow: 'ellipsis',
                        whiteSpace: 'nowrap',
                        maxWidth: 200
                      }}
                    >
                      {content.content.substring(0, 50)}...
                    </Typography>
                  </Box>
                </TableCell>
                <TableCell>
                  <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    {getTypeIcon(content.type)}
                    <Typography variant="body2">
                      {getTypeText(content.type)}
                    </Typography>
                  </Box>
                </TableCell>
                <TableCell>
                  <Chip
                    label={getStatusText(content.status)}
                    color={getStatusColor(content.status)}
                    size="small"
                  />
                </TableCell>
                <TableCell>
                  <Typography variant="body2" sx={{ fontWeight: 500 }}>
                    {content.views.toLocaleString()}
                  </Typography>
                </TableCell>
                <TableCell>
                  <Typography variant="body2">
                    {content.updatedAt}
                  </Typography>
                </TableCell>
                <TableCell>
                  <Box sx={{ display: 'flex', gap: 0.5 }}>
                    <IconButton 
                      size="small" 
                      color="info"
                      onClick={() => handleView(content.id)}
                    >
                      <VisibilityIcon fontSize="small" />
                    </IconButton>
                    <IconButton 
                      size="small" 
                      color="primary"
                      onClick={() => handleEdit(content)}
                    >
                      <EditIcon fontSize="small" />
                    </IconButton>
                    <IconButton 
                      size="small" 
                      color="error"
                      onClick={() => handleDelete(content.id)}
                    >
                      <DeleteIcon fontSize="small" />
                    </IconButton>
                  </Box>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      {/* Pagination */}
      {totalPages > 1 && (
        <Box sx={{ display: 'flex', justifyContent: 'center', mt: 2 }}>
          <Pagination
            count={totalPages}
            page={page}
            onChange={(event, newPage) => setPage(newPage)}
            color="primary"
            size="medium"
          />
        </Box>
      )}

      {/* Add/Edit Dialog */}
      <Dialog open={openDialog} onClose={() => setOpenDialog(false)} maxWidth="md" fullWidth>
        <DialogTitle>
          {editingContent ? 'Chnh sa ni dung' : 'Thm ni dung mi'}
        </DialogTitle>
        <DialogContent>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, mt: 1 }}>
            <TextField
              label="Tiu "
              value={formData.title}
              onChange={(e) => setFormData({ ...formData, title: e.target.value })}
              fullWidth
              required
            />
            <TextField
              label="Ni dung"
              value={formData.content}
              onChange={(e) => setFormData({ ...formData, content: e.target.value })}
              multiline
              rows={4}
              fullWidth
              required
            />
            <Box sx={{ display: 'flex', gap: 2 }}>
              <FormControl sx={{ minWidth: 150 }}>
                <InputLabel>Loi ni dung</InputLabel>
                <Select
                  value={formData.type}
                  onChange={(e) => setFormData({ ...formData, type: e.target.value })}
                  label="Loi ni dung"
                >
                  <MenuItem value="article">Bi vit</MenuItem>
                  <MenuItem value="video">Video</MenuItem>
                  <MenuItem value="image">Hnh nh</MenuItem>
                  <MenuItem value="announcement">Thng bo</MenuItem>
                </Select>
              </FormControl>
              <FormControl sx={{ minWidth: 150 }}>
                <InputLabel>Trng thi</InputLabel>
                <Select
                  value={formData.status}
                  onChange={(e) => setFormData({ ...formData, status: e.target.value })}
                  label="Trng thi"
                >
                  <MenuItem value="draft">Bn nhp</MenuItem>
                  <MenuItem value="review">ang duyt</MenuItem>
                  <MenuItem value="published"> xut bn</MenuItem>
                  <MenuItem value="archived">Lu tr</MenuItem>
                </Select>
              </FormControl>
            </Box>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpenDialog(false)}>Hy</Button>
          <Button 
            onClick={handleSave} 
            variant="contained"
            disabled={!formData.title || !formData.content}
          >
            {editingContent ? 'Cp nht' : 'Thm mi'}
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default ContentTable;
