import React, { useEffect, useState } from 'react';
import SearchIcon from '@mui/icons-material/Search';
import Sidebar from '../../components/admin/AdminSidebar';
import Header from '../../components/admin/AdminHeader';
import { 
  Box, 
  Typography, 
  InputBase, 
  Button, 
  Table, 
  TableHead, 
  TableBody, 
  TableRow, 
  TableCell, 
  Paper, 
  TableContainer, 
  Pagination, 
  IconButton,
  Tab,
  Tabs,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Chip,
  Grid
} from '@mui/material';
import { useLocation } from 'react-router-dom';
import { toast } from 'react-toastify';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import VisibilityIcon from '@mui/icons-material/Visibility';
import ScheduleIcon from '@mui/icons-material/Schedule';
import CalendarTodayIcon from '@mui/icons-material/CalendarToday';

export default function AdminCounselor() {
  const location = useLocation();
  const [searchTerm, setSearchTerm] = useState('');
  const [counselors, setCounselors] = useState([]);
  const [page, setPage] = useState(1);
  const pageSize = 6;
  const [totalPages, setTotalPages] = useState(1);
  const [activeTab, setActiveTab] = useState(0);
  const [scheduleDialogOpen, setScheduleDialogOpen] = useState(false);
  const [selectedCounselor, setSelectedCounselor] = useState(null);
  const [schedules, setSchedules] = useState({});

  useEffect(() => {
    if (location.state?.toastMessage) {
      toast.success(location.state.toastMessage);
      window.history.replaceState({}, document.title);
    }
  }, [location.state]);

  useEffect(() => {
    fetchCounselors();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const fetchCounselors = async () => {
    try {
      // Mock data for counselors - replace with actual API call
      const mockCounselors = [
        {
          id: 1,
          name: 'Nguyn Th Lan',
          email: 'lan.nguyen@gynexa.com',
          specialization: 'T vn sc khe sinh sn',
          status: 'active',
          experience: '5 nm',
          rating: 4.8,
          consultations: 245
        },
        {
          id: 2,
          name: 'Trn Vn Minh',
          email: 'minh.tran@gynexa.com',
          specialization: 'T vn tm l',
          status: 'inactive',
          experience: '3 nm',
          rating: 4.6,
          consultations: 189
        },
        {
          id: 3,
          name: 'L Th Hoa',
          email: 'hoa.le@gynexa.com',
          specialization: 'T vn HIV/AIDS',
          status: 'active',
          experience: '7 nm',
          rating: 4.9,
          consultations: 312
        },
        {
          id: 4,
          name: 'Phm c Thnh',
          email: 'thinh.pham@gynexa.com',
          specialization: 'T vn dinh dng',
          status: 'active',
          experience: '4 nm',
          rating: 4.5,
          consultations: 156
        },
        {
          id: 5,
          name: 'Hong Th Mai',
          email: 'mai.hoang@gynexa.com',
          specialization: 'T vn k hoch ha gia nh',
          status: 'active',
          experience: '6 nm',
          rating: 4.7,
          consultations: 203
        },
        {
          id: 6,
          name: 'V Vn Hng',
          email: 'hung.vo@gynexa.com',
          specialization: 'T vn sc khe nam gii',
          status: 'inactive',
          experience: '4 nm',
          rating: 4.4,
          consultations: 127
        }
      ];
      setCounselors(mockCounselors);
      setTotalPages(Math.ceil(mockCounselors.length / pageSize));
      setPage(1);
      
      // Initialize mock schedules
      const mockSchedules = {};
      mockCounselors.forEach(counselor => {
        mockSchedules[counselor.id] = generateMockSchedule();
      });
      setSchedules(mockSchedules);
    } catch (err) {
      console.error('Error fetching counselors:', err);
    }
  };

  const generateMockSchedule = () => {
    const days = ['Th 2', 'Th 3', 'Th 4', 'Th 5', 'Th 6', 'Th 7', 'Ch nht'];
    const timeSlots = ['8:00-10:00', '10:00-12:00', '14:00-16:00', '16:00-18:00', '19:00-21:00'];
    const schedule = {};
    
    days.forEach(day => {
      schedule[day] = timeSlots.filter(() => Math.random() > 0.4);
    });
    
    return schedule;
  };

  useEffect(() => {
    const filtered = counselors.filter(counselor => 
      counselor.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      counselor.specialization.toLowerCase().includes(searchTerm.toLowerCase())
    );
    setTotalPages(Math.ceil(filtered.length / pageSize));
    setPage(1);
  }, [searchTerm, counselors]);

  const handleChangePage = (event, value) => {
    setPage(value);
  };

  const handleEditCounselor = (counselorId) => {
    toast.info(`Chnh sa t vn vin ID: ${counselorId}`);
    // Implement edit logic here
  };

  const handleDeleteCounselor = (counselorId) => {
    toast.warning(`Xa t vn vin ID: ${counselorId}`);
    // Implement delete logic here
  };

  const handleViewCounselor = (counselorId) => {
    toast.info(`Xem chi tit t vn vin ID: ${counselorId}`);
    // Implement view logic here
  };

  const handleAddCounselor = () => {
    toast.info('Thm t vn vin mi');
    // Implement add logic here
  };

  const handleManageSchedule = (counselor) => {
    setSelectedCounselor(counselor);
    setScheduleDialogOpen(true);
  };

  const handleScheduleDialogClose = () => {
    setScheduleDialogOpen(false);
    setSelectedCounselor(null);
  };

  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };

  const filteredCounselors = counselors.filter(counselor => 
    counselor.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    counselor.specialization.toLowerCase().includes(searchTerm.toLowerCase())
  );
  const paginatedCounselors = filteredCounselors.slice((page - 1) * pageSize, page * pageSize);

  return (
    <Box sx={{ display: 'flex', height: '100vh' }}>
      <Sidebar />
      <Box sx={{ flexGrow: 1, display: 'flex', flexDirection: 'column' }}>
        <Box sx={{ flexShrink: 0, minHeight: 64 }}>
          <Header />
        </Box>

        <Box sx={{ flexGrow: 1, bgcolor: '#f5f5f5', p: 2, overflowY: 'auto' }}>
          <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', px: 2, mb: 2, mt: 2 }}>
            <Typography variant='h3' sx={{ fontWeight: 500, color: 'gray'  }}>
              Qun l t vn vin
            </Typography>

            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <Button
                variant="contained"
                onClick={handleAddCounselor}
                sx={{
                  bgcolor: '#3B6774',
                  '&:hover': { bgcolor: '#2d5a66' },
                  borderRadius: '999px',
                  px: 3,
                  py: 1
                }}
              >
                + Thm t vn vin
              </Button>

              {/* Search box */}
              <Box sx={{ display: 'flex', alignItems: 'center', width: 300, bgcolor: 'white', borderRadius: '999px', px: 2, py: '6px', boxShadow: '0 0 0 1px #ccc' }}>
                <SearchIcon sx={{ color: 'gray', mr: 1 }} />
                <InputBase
                  placeholder="Tm kim t vn vin..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  sx={{
                    flex: 1,
                    color: 'gray',
                    fontSize: 16,
                    '& .MuiInputBase-input': { p: 0 },
                    '&:focus-within': { outline: 'none' }
                  }}
                />
              </Box>
            </Box>
          </Box>

          {/* TABLE AREA */}
          <Box sx={{ bgcolor: '#f5f5f5', p: 2 }}>
            <TableContainer component={Paper} sx={{ borderRadius: 3 }}>
              <Table>
                <TableHead sx={{ backgroundColor: '#3B6774' }}>
                  <TableRow>
                    <TableCell sx={{ color: '#f5f5f5' }}>STT</TableCell>
                    <TableCell sx={{ color: '#f5f5f5' }}>H tn</TableCell>
                    <TableCell sx={{ color: '#f5f5f5' }}>Email</TableCell>
                    <TableCell sx={{ color: '#f5f5f5' }}>Chuyn mn</TableCell>
                    <TableCell sx={{ color: '#f5f5f5' }}>Trng thi</TableCell>
                    <TableCell sx={{ color: '#f5f5f5' }}>S ca</TableCell>
                    <TableCell sx={{ color: '#f5f5f5' }}>nh gi</TableCell>
                    <TableCell sx={{ color: '#f5f5f5' }} align="center">Lch lm vic</TableCell>
                    <TableCell sx={{ color: '#f5f5f5' }} align="center">Thao tc</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {paginatedCounselors.map((counselor, index) => (
                    <TableRow key={counselor.id}>
                      <TableCell>{(page - 1) * pageSize + index + 1}</TableCell>
                      <TableCell>{counselor.name}</TableCell>
                      <TableCell>{counselor.email}</TableCell>
                      <TableCell>{counselor.specialization}</TableCell>
                      <TableCell>
                        <span style={{ 
                          color: counselor.status === 'active' ? 'green' : 'orange',
                          fontWeight: 'bold',
                          padding: '4px 8px',
                          borderRadius: '4px',
                          backgroundColor: counselor.status === 'active' ? '#e8f5e8' : '#fff3cd'
                        }}>
                          {counselor.status === 'active' ? 'ang online' : 'Tm dng'}
                        </span>
                      </TableCell>
                      <TableCell>{counselor.consultations}</TableCell>
                      <TableCell> {counselor.rating}</TableCell>
                      <TableCell align="center">
                        <Button
                          variant="outlined"
                          size="small"
                          startIcon={<ScheduleIcon />}
                          onClick={() => handleManageSchedule(counselor)}
                          sx={{
                            color: '#3B6774',
                            borderColor: '#3B6774',
                            '&:hover': {
                              backgroundColor: '#f0f7f9',
                              borderColor: '#2d5a66'
                            }
                          }}
                        >
                          Qun l
                        </Button>
                      </TableCell>
                      <TableCell align="center">
                        <IconButton 
                          color="info" 
                          size="small" 
                          title="Xem chi tit"
                          onClick={() => handleViewCounselor(counselor.id)}
                        >
                          <VisibilityIcon />
                        </IconButton>
                        <IconButton 
                          color="primary" 
                          size="small" 
                          title="Chnh sa"
                          onClick={() => handleEditCounselor(counselor.id)}
                        >
                          <EditIcon />
                        </IconButton>
                        <IconButton 
                          color="error" 
                          size="small" 
                          title="Xa"
                          onClick={() => handleDeleteCounselor(counselor.id)}
                        >
                          <DeleteIcon />
                        </IconButton>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>

            <Box sx={{ display: 'flex', justifyContent: 'center', mt: 2 }}>
              <Pagination count={totalPages} page={page} onChange={handleChangePage} />
            </Box>
          </Box>
        </Box>
      </Box>

      {/* Schedule Management Dialog */}
      <Dialog
        open={scheduleDialogOpen}
        onClose={handleScheduleDialogClose}
        maxWidth="lg"
        fullWidth
      >
        <DialogTitle>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <CalendarTodayIcon sx={{ color: '#3B6774' }} />
            <Typography variant="h6">
              Lch lm vic - {selectedCounselor?.name}
            </Typography>
          </Box>
        </DialogTitle>
        <DialogContent>
          <Tabs value={activeTab} onChange={handleTabChange} sx={{ borderBottom: 1, borderColor: 'divider', mb: 2 }}>
            <Tab label="Xem lch" />
            <Tab label="Chnh sa lch" />
          </Tabs>

          {activeTab === 0 && selectedCounselor && schedules[selectedCounselor.id] && (
            <Box>
              <Typography variant="h6" sx={{ mb: 2, color: '#3B6774' }}>
                Lch lm vic tun ny
              </Typography>
              <Grid container spacing={2}>
                {Object.entries(schedules[selectedCounselor.id]).map(([day, timeSlots]) => (
                  <Grid item xs={12} md={6} lg={4} key={day}>
                    <Paper sx={{ p: 2, borderRadius: 2, border: '1px solid #e0e0e0' }}>
                      <Typography variant="subtitle1" sx={{ fontWeight: 'bold', mb: 1, color: '#3B6774' }}>
                        {day}
                      </Typography>
                      {timeSlots.length > 0 ? (
                        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1 }}>
                          {timeSlots.map((slot, index) => (
                            <Chip
                              key={index}
                              label={slot}
                              variant="outlined"
                              size="small"
                              sx={{
                                backgroundColor: '#e8f5e8',
                                borderColor: '#4caf50',
                                color: '#2e7d32'
                              }}
                            />
                          ))}
                        </Box>
                      ) : (
                        <Typography variant="body2" sx={{ color: '#999', fontStyle: 'italic' }}>
                          Khng c lch lm vic
                        </Typography>
                      )}
                    </Paper>
                  </Grid>
                ))}
              </Grid>
            </Box>
          )}

          {activeTab === 1 && selectedCounselor && (
            <Box>
              <Typography variant="h6" sx={{ mb: 2, color: '#3B6774' }}>
                Chnh sa lch lm vic
              </Typography>
              <Grid container spacing={2}>
                {['Th 2', 'Th 3', 'Th 4', 'Th 5', 'Th 6', 'Th 7', 'Ch nht'].map((day) => (
                  <Grid item xs={12} md={6} key={day}>
                    <Paper sx={{ p: 2, borderRadius: 2, border: '1px solid #e0e0e0' }}>
                      <Typography variant="subtitle1" sx={{ fontWeight: 'bold', mb: 2, color: '#3B6774' }}>
                        {day}
                      </Typography>
                      <FormControl fullWidth size="small" sx={{ mb: 2 }}>
                        <InputLabel>Chn ca lm vic</InputLabel>
                        <Select
                          multiple
                          value={schedules[selectedCounselor?.id]?.[day] || []}
                          label="Chn ca lm vic"
                          renderValue={(selected) => (
                            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                              {selected.map((value) => (
                                <Chip key={value} label={value} size="small" />
                              ))}
                            </Box>
                          )}
                          MenuProps={{
                            PaperProps: {
                              sx: { maxHeight: 200 }
                            },
                            disablePortal: true,
                            keepMounted: false
                          }}
                          onChange={(e) => {
                            const newSchedules = { ...schedules };
                            if (!newSchedules[selectedCounselor.id]) {
                              newSchedules[selectedCounselor.id] = {};
                            }
                            newSchedules[selectedCounselor.id][day] = e.target.value;
                            setSchedules(newSchedules);
                          }}
                        >
                          {['8:00-10:00', '10:00-12:00', '14:00-16:00', '16:00-18:00', '19:00-21:00'].map((timeSlot) => (
                            <MenuItem key={timeSlot} value={timeSlot}>
                              {timeSlot}
                            </MenuItem>
                          ))}
                        </Select>
                      </FormControl>
                    </Paper>
                  </Grid>
                ))}
              </Grid>
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleScheduleDialogClose} color="inherit">
            Hy
          </Button>
          {activeTab === 1 && (
            <Button 
              variant="contained" 
              sx={{ 
                bgcolor: '#3B6774', 
                '&:hover': { bgcolor: '#2d5a66' } 
              }}
              onClick={() => {
                toast.success('Cp nht lch lm vic thnh cng!');
                handleScheduleDialogClose();
              }}
            >
              Lu thay i
            </Button>
          )}
        </DialogActions>
      </Dialog>
    </Box>
  );
}
