import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../../../context/AuthContext';
import { useAppointment } from '../../../context/AppointmentContext';
import {
  Calendar,
  Clock,
  Star,
  User,
  MessageCircle,
  Video,
  Phone,
  X,
  Users,
  ArrowRight,
  Edit,
  Eye,
} from 'lucide-react';
import { format, addDays } from 'date-fns';
import { toast } from 'react-toastify';
//import NotificationService from '../../services/NotificationService';
import './index.css';

const Consultation = () => {
  const user = { email: 'user@example.com' }; // Gi lp user c email
  const {
    appointments,
    bookAppointment,
    cancelAppointment,
    getAvailableCounselors,
    getCounselorById,
  } = useAppointment();
  const [selectedCounselor, setSelectedCounselor] = useState(null);
  const [selectedDate, setSelectedDate] = useState('');
  const [selectedTime, setSelectedTime] = useState('');
  const [consultationType, setConsultationType] = useState('video'); // video, phone, chat
  const [reason, setReason] = useState('');
  const [showBookingForm, setShowBookingForm] = useState(false);
  const [activeTab, setActiveTab] = useState('book'); // book, history
  const [highlightedAppointmentId, setHighlightedAppointmentId] =
    useState(null);

  const availableCounselors = getAvailableCounselors();

  // Generate next 14 days for booking
  const availableDates = Array.from({ length: 14 }, (_, i) =>
    addDays(new Date(), i)
  );

  const consultationTypes = [
    {
      id: 'video',
      name: 'Video call',
      description: 'T vn trc tuyn qua video call, bo mt v tin li.',
      icon: Video,
      price: '300.000',
    },
    {
      id: 'phone',
      name: 'Gi in thoi',
      description: 'T vn qua in thoi, ring t v nhanh chng.',
      icon: Phone,
      price: '200.000',
    },
    {
      id: 'chat',
      name: 'Chat trc tuyn',
      description: 'T vn qua chat, d dng lu li ni dung.',
      icon: MessageCircle,
      price: '150.000',
    },
  ];

  const timeSlots = selectedCounselor ? selectedCounselor.timeSlots : [];

  // Ly cc slot  c t ca counselor trong ngy
  const getUnavailableSlots = (counselorId, date) => {
    if (!counselorId || !date) return [];
    const dateStr = format(new Date(date), 'yyyy-MM-dd');
    return appointments
      .filter(
        appointment =>
          appointment.counselorId === counselorId &&
          appointment.date === dateStr &&
          ['scheduled', 'rescheduled'].includes(appointment.status)
      )
      .map(appointment => appointment.time);
  };

  // Kim tra trng lch
  const checkAppointmentConflict = (counselorId, date, time) => {
    return appointments.some(
      appointment =>
        appointment.counselorId === counselorId &&
        appointment.date === date &&
        appointment.time === time &&
        ['scheduled', 'rescheduled'].includes(appointment.status)
    );
  };

  // Kim tra slot  qua
  const isPastSlot = (date, time) => {
    const slotDateTime = new Date(
      `${format(new Date(date), 'yyyy-MM-dd')}T${time}`
    );
    return slotDateTime <= new Date();
  };

  const handleBookAppointment = () => {
    if (!selectedCounselor) {
      toast.error('Vui lng chn t vn vin');
      return;
    }
    if (!selectedDate) {
      toast.error('Vui lng chn ngy t vn');
      return;
    }
    if (!selectedTime) {
      toast.error('Vui lng chn gi t vn');
      return;
    }
    if (!consultationType) {
      toast.error('Vui lng chn hnh thc t vn');
      return;
    }
    // Format date
    const appointmentDate = format(new Date(selectedDate), 'yyyy-MM-dd');
    // Check conflict
    if (
      checkAppointmentConflict(
        selectedCounselor.id,
        appointmentDate,
        selectedTime
      )
    ) {
      toast.error(
        'Thi gian ny  c ngi t. Vui lng chn thi gian khc.'
      );
      return;
    }
    // Check qu kh
    if (isPastSlot(appointmentDate, selectedTime)) {
      toast.error(
        'Khng th t lch trong qu kh. Vui lng chn thi gian khc.'
      );
      return;
    }
    // To d liu lch hn
    const appointmentData = {
      counselorId: selectedCounselor.id,
      counselorName: selectedCounselor.name,
      date: appointmentDate,
      time: selectedTime,
      type: consultationType,
      reason: reason.trim(),
    };
    const newApt = bookAppointment(appointmentData);
    toast.success('t lch t vn thnh cng!', {
      autoClose: 4000,
      className: 'booking-success-toast',
    });
    // Gi email xc nhn (mock)
    if (user?.email) {
      NotificationService.sendBookingConfirmationEmail({
        to: user.email,
        counselorName: selectedCounselor.name,
        date: appointmentDate,
        time: selectedTime,
        type: consultationType,
      });
      // Ln lch gi email nhc nh trc 30 pht (mock)
      const selectedDateTime = new Date(`${appointmentDate}T${selectedTime}`);
      const now = new Date();
      const msToReminder = selectedDateTime - now - 30 * 60 * 1000;
      if (msToReminder > 0) {
        setTimeout(() => {
          NotificationService.sendAppointmentReminderEmail({
            to: user.email,
            counselorName: selectedCounselor.name,
            date: appointmentDate,
            time: selectedTime,
            type: consultationType,
          });
        }, msToReminder);
      }
    }
    // Reset form
    setReason('');
    setShowBookingForm(false);
    setSelectedTime('');
    setActiveTab('history');
    setHighlightedAppointmentId(newApt.id);
  };

  const handleCancelAppointment = appointmentId => {
    cancelAppointment(appointmentId);
    toast.info(' hy lch hn.');
  };

  const getStatusText = status => {
    switch (status) {
      case 'scheduled':
        return 'Sp din ra';
      case 'completed':
        return 'Hon thnh';
      case 'cancelled':
        return ' hy';
      case 'rescheduled':
        return ' di lch';
      default:
        return status;
    }
  };

  return (
    <div className="consultation-container">
      {/* Header */}
      <div className="consultation-header">
        <div className="consultation-header-content">
          <h1 className="consultation-title">T vn trc tuyn</h1>
          <p className="consultation-subtitle">
            t lch t vn vi cc chuyn gia ca chng ti
          </p>
        </div>
      </div>

      <div className="consultation-main">
        {/* Tab Navigation */}
        <div className="consultation-tabs">
          <button
            className={`tab-btn ${activeTab === 'book' ? 'active' : ''}`}
            onClick={() => setActiveTab('book')}
          >
            <Calendar size={20} />
            t lch t vn
          </button>
          <button
            className={`tab-btn ${activeTab === 'history' ? 'active' : ''}`}
            onClick={() => setActiveTab('history')}
          >
            <Clock size={20} />
            Lch hn ca ti ({appointments.length})
          </button>
        </div>

        {/* Book Appointment Tab */}
        {activeTab === 'book' && (
          <>
            {/* Consultation Types */}
            <div className="consultation-types-section">
              <div className="consultation-types-grid">
                {consultationTypes.map(type => {
                  const Icon = type.icon;
                  return (
                    <div
                      key={type.id}
                      className={`consultation-type-card ${
                        consultationType === type.id ? 'selected' : ''
                      }`}
                      onClick={() => setConsultationType(type.id)}
                    >
                      <div className="type-header">
                        <Icon size={32} />
                        <div className="type-info">
                          <h3>{type.name}</h3>
                          <p>{type.description}</p>
                        </div>
                        <div className="type-price">{type.price}</div>
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>

            {/* Available Counselors */}
            <div className="counselors-section">
              <h2 className="section-title">Chn t vn vin</h2>
              <div className="counselors-grid">
                {availableCounselors.map(counselor => (
                  <div
                    key={counselor.id}
                    className={`counselor-card ${
                      selectedCounselor?.id === counselor.id ? 'selected' : ''
                    }`}
                    onClick={() => setSelectedCounselor(counselor)}
                  >
                    <div className="counselor-avatar">
                      <img
                        src={counselor.avatar}
                        alt={counselor.name}
                        style={{ width: 56, height: 56, borderRadius: '50%' }}
                      />
                    </div>
                    <div className="counselor-info">
                      <h4>{counselor.name}</h4>
                      <div className="counselor-specialty">
                        {counselor.specialty}
                      </div>
                      <div className="counselor-rating">
                        <Star
                          size={16}
                          color="#FFD700"
                          style={{ marginRight: 4 }}
                        />
                        <span style={{ fontWeight: 600 }}>
                          {counselor.rating}
                        </span>
                        <span style={{ color: '#888', marginLeft: 8 }}>
                          <Users size={14} style={{ marginRight: 2 }} />
                          {counselor.consultationCount} bui t vn
                        </span>
                      </div>
                    </div>
                    <div className="counselor-details">
                      <p>Kinh nghim: {counselor.experience}</p>
                      <p>Chuyn mn: {counselor.specialty}</p>
                    </div>
                  </div>
                ))}
              </div>
            </div>

            {/* Book Button */}
            {consultationType && selectedCounselor && (
              <div className="book-section">
                <button
                  onClick={() => setShowBookingForm(true)}
                  className="book-consultation-btn"
                >
                  t lch t vn vi {selectedCounselor.name}
                  <ArrowRight size={20} />
                </button>
              </div>
            )}

            {/* Booking Form Modal */}
            {showBookingForm && (
              <div
                className="booking-form-overlay"
                onClick={() => setShowBookingForm(false)}
              >
                <div
                  className="booking-form-container"
                  onClick={e => e.stopPropagation()}
                >
                  <button
                    className="form-btn close"
                    onClick={() => setShowBookingForm(false)}
                  >
                    <X size={20} />
                  </button>
                  <h2 className="section-title">t lch t vn</h2>
                  {/* Date Selection */}
                  <div className="form-section">
                    <h3 className="form-section-title">Chn ngy *</h3>
                    <div className="date-grid">
                      {availableDates.map(date => {
                        const isPastDate =
                          date < new Date().setHours(0, 0, 0, 0);
                        return (
                          <div
                            key={date.toString()}
                            className={`date-option ${
                              selectedDate === date.toString() ? 'selected' : ''
                            } ${isPastDate ? 'past-date' : ''}`}
                            onClick={() => {
                              if (!isPastDate) {
                                setSelectedDate(date.toString());
                                setSelectedTime('');
                              }
                            }}
                            title={
                              isPastDate
                                ? 'Khng th chn ngy trong qu kh'
                                : ''
                            }
                          >
                            <div className="date-day">
                              {format(date, 'EEE')}
                            </div>
                            <div className="date-number">
                              {format(date, 'dd/MM')}
                            </div>
                          </div>
                        );
                      })}
                    </div>
                  </div>
                  {/* Time Selection */}
                  {selectedDate && (
                    <div className="form-section">
                      <h3 className="form-section-title">Chn gi *</h3>
                      <div className="time-grid">
                        {timeSlots.map(time => {
                          const unavailableSlots = getUnavailableSlots(
                            selectedCounselor.id,
                            selectedDate
                          );
                          const isUnavailable = unavailableSlots.includes(time);
                          const isPastTime = isPastSlot(selectedDate, time);
                          return (
                            <div
                              key={time}
                              className={`time-option ${
                                selectedTime === time ? 'selected' : ''
                              } ${
                                isUnavailable || isPastTime ? 'unavailable' : ''
                              }`}
                              onClick={() => {
                                if (!isUnavailable && !isPastTime) {
                                  setSelectedTime(time);
                                }
                              }}
                              title={
                                isUnavailable
                                  ? ' c ngi t'
                                  : isPastTime
                                  ? ' qua gi'
                                  : ''
                              }
                            >
                              {time}
                              {isUnavailable && (
                                <span className="unavailable-badge">
                                   t
                                </span>
                              )}
                            </div>
                          );
                        })}
                      </div>
                    </div>
                  )}
                  {/* Reason */}
                  <div className="form-section">
                    <h3 className="form-section-title">
                      L do t vn (ty chn)
                    </h3>
                    <textarea
                      value={reason}
                      onChange={e => setReason(e.target.value)}
                      placeholder="M t ngn gn vn  bn mun t vn..."
                      className="reason-textarea"
                      maxLength={500}
                    />
                  </div>
                  {/* Confirm Button */}
                  <div className="form-section" style={{ textAlign: 'center' }}>
                    <button
                      className="form-btn confirm"
                      onClick={handleBookAppointment}
                    >
                      Xc nhn t lch
                    </button>
                  </div>
                </div>
              </div>
            )}
          </>
        )}

        {/* My Appointments Tab */}
        {activeTab === 'history' && (
          <div className="appointments-history">
            <h2 className="section-title">Lch hn ca ti</h2>
            {appointments.length > 0 ? (
              <div className="appointments-grid">
                {appointments
                  .sort((a, b) => {
                    const dA = new Date(`${a.date}T${a.time}`);
                    const dB = new Date(`${b.date}T${b.time}`);
                    return dA - dB;
                  })
                  .map(appointment => {
                    const counselor = getCounselorById(appointment.counselorId);
                    const isUpcoming =
                      new Date(`${appointment.date}T${appointment.time}`) >
                      new Date();
                    const canCancel =
                      isUpcoming && appointment.status === 'scheduled';
                    const canJoin =
                      !canCancel && appointment.status === 'scheduled';
                    return (
                      <div
                        key={appointment.id}
                        className={`appointment-card${
                          highlightedAppointmentId === appointment.id
                            ? ' booking-success-animation'
                            : ''
                        }`}
                        style={
                          highlightedAppointmentId === appointment.id
                            ? { border: '2px solid #568392' }
                            : {}
                        }
                      >
                        <div className="appointment-header">
                          <div className="appointment-status">
                            {getStatusText(appointment.status)}
                          </div>
                          <div className="appointment-actions">
                            <button className="action-btn" title="Xem chi tit">
                              <Eye size={16} />
                            </button>
                            {canCancel && (
                              <button
                                className="action-btn"
                                title="Hy lch"
                                onClick={() =>
                                  handleCancelAppointment(appointment.id)
                                }
                              >
                                <Edit size={16} />
                              </button>
                            )}
                          </div>
                        </div>
                        <div className="appointment-content">
                          <div className="appointment-counselor">
                            <div className="counselor-avatar-small">
                              <User />
                            </div>
                            <div>
                              <h4>{appointment.counselorName}</h4>
                              <p>{counselor?.specialty || 'T vn vin'}</p>
                            </div>
                          </div>
                          <div className="appointment-details">
                            <div className="detail-item">
                              <Calendar size={16} />
                              <span>{appointment.date}</span>
                            </div>
                            <div className="detail-item">
                              <Clock size={16} />
                              <span>{appointment.time}</span>
                            </div>
                            <div className="detail-item">
                              {appointment.type === 'video' && (
                                <Video size={16} />
                              )}
                              {appointment.type === 'phone' && (
                                <Phone size={16} />
                              )}
                              {appointment.type === 'chat' && (
                                <MessageCircle size={16} />
                              )}
                              <span>
                                {
                                  consultationTypes.find(
                                    t => t.id === appointment.type
                                  )?.name
                                }
                              </span>
                            </div>
                          </div>
                          {appointment.reason && (
                            <div className="appointment-reason">
                              <p>
                                <strong>L do t vn:</strong>{' '}
                                {appointment.reason}
                              </p>
                            </div>
                          )}
                          {canJoin && (
                            <div className="appointment-cta">
                              <button className="join-btn">
                                Tham gia t vn
                                <ArrowRight size={16} />
                              </button>
                            </div>
                          )}
                        </div>
                      </div>
                    );
                  })}
              </div>
            ) : (
              <div className="empty-appointments">
                <div className="empty-icon">
                  <Calendar size={64} />
                </div>
                <h3>Cha c lch hn no</h3>
                <p>
                  Bn cha t lch t vn no. Hy t lch vi cc chuyn gia
                  ca chng ti!
                </p>
                <button
                  className="book-now-btn"
                  onClick={() => setActiveTab('book')}
                >
                  t lch ngay
                  <ArrowRight size={16} />
                </button>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default Consultation;
