import React, { createContext, useContext, useState } from 'react';

const AppointmentContext = createContext();

export const useAppointment = () => {
  const context = useContext(AppointmentContext);
  if (!context) {
    throw new Error(
      'useAppointment must be used within an AppointmentProvider'
    );
  }
  return context;
};

export const AppointmentProvider = ({ children }) => {
  const [appointments, setAppointments] = useState([
    {
      id: 1,
      counselorId: 1,
      counselorName: 'Dr. Nguyn Th Hng',
      date: '2024-12-25',
      time: '09:00',
      type: 'video',
      reason: 'T vn v chu k kinh nguyt khng u',
      status: 'scheduled',
      createdAt: new Date('2024-12-20'),
    },
    {
      id: 2,
      counselorId: 3,
      counselorName: 'Dr. Phm Th Mai',
      date: '2024-12-22',
      time: '14:30',
      type: 'chat',
      reason: 'Hi v cc phng php trnh thai',
      status: 'completed',
      createdAt: new Date('2024-12-18'),
    },
    {
      id: 3,
      counselorId: 2,
      counselorName: 'Dr. L Vn Minh',
      date: '2024-12-28',
      time: '10:00',
      type: 'phone',
      reason: 'T vn tm l v stress cng vic',
      status: 'scheduled',
      createdAt: new Date('2024-12-21'),
    },
  ]);

  const [counselors, setCounselors] = useState([
    {
      id: 1,
      name: 'Dr. Nguyn Th Hng',
      specialty: 'Chuyn khoa Sn Ph khoa',
      experience: '12+ nm kinh nghim',
      rating: 4.9,
      avatar: '/images/doctor1.jpg',
      available: true,
      timeSlots: ['09:00', '10:00', '11:00', '14:00', '15:00', '16:00'],
      bio: 'Bc s chuyn khoa I, c kinh nghim lu nm trong lnh vc sn ph khoa. Chuyn iu tr cc bnh l ph khoa, t vn k hoch ha gia nh.',
      education: 'i hc Y H Ni',
      languages: ['Ting Vit', 'English'],
      consultationCount: 1250,
    },
    {
      id: 2,
      name: 'Dr. L Vn Minh',
      specialty: 'T vn tm l & Sc khe tnh dc',
      experience: '8+ nm kinh nghim',
      rating: 4.8,
      avatar: '/images/doctor2.jpg',
      available: true,
      timeSlots: ['09:30', '10:30', '11:30', '14:30', '15:30', '16:30'],
      bio: 'Chuyn gia t vn tm l, tnh dc hc. C nhiu kinh nghim trong vic t vn cc vn  v sc khe tnh dc v mi quan h.',
      education: 'i hc Y dc TP.HCM',
      languages: ['Ting Vit', 'English'],
      consultationCount: 890,
    },
    {
      id: 3,
      name: 'Dr. Phm Th Mai',
      specialty: 'Dinh dng & K hoch ha gia nh',
      experience: '10+ nm kinh nghim',
      rating: 4.7,
      avatar: '/images/doctor3.jpg',
      available: true,
      timeSlots: ['08:30', '09:00', '10:00', '13:30', '14:00', '15:00'],
      bio: 'Chuyn gia dinh dng v k hoch ha gia nh. T vn v ch  n ung, b sung vitamin v cc phng php trnh thai hin i.',
      education: 'i hc Y Hu',
      languages: ['Ting Vit'],
      consultationCount: 650,
    },
    {
      id: 4,
      name: 'Dr. Trn Minh Tun',
      specialty: 'Chuyn khoa Nam hc',
      experience: '15+ nm kinh nghim',
      rating: 4.9,
      avatar: '/images/doctor4.jpg',
      available: true,
      timeSlots: ['09:00', '10:00', '11:00', '14:00', '15:00', '16:00'],
      bio: 'Bc s chuyn khoa II Nam hc, c kinh nghim phong ph trong iu tr cc bnh l nam khoa v t vn sc khe sinh sn nam gii.',
      education: 'i hc Y H Ni',
      languages: ['Ting Vit', 'English', ''],
      consultationCount: 1100,
    },
    {
      id: 5,
      name: 'Dr. Hong Th Lan',
      specialty: 'Chm sc sc khe thiu nin',
      experience: '7+ nm kinh nghim',
      rating: 4.6,
      avatar: '/images/doctor5.jpg',
      available: true,
      timeSlots: ['08:00', '09:00', '10:00', '13:00', '14:00', '15:00'],
      bio: 'Chuyn gia t vn sc khe sinh sn cho thanh thiu nin. Kinh nghim trong vic gio dc gii tnh v t vn cho  tui dy th.',
      education: 'i hc Y dc Cn Th',
      languages: ['Ting Vit', 'English'],
      consultationCount: 420,
    },
    {
      id: 6,
      name: 'Dr. V c Ha',
      specialty: 'iu tr v sinh him mun',
      experience: '20+ nm kinh nghim',
      rating: 4.8,
      avatar: '/images/doctor6.jpg',
      available: false, // ang bn
      timeSlots: ['09:00', '10:00', '11:00', '14:00', '15:00'],
      bio: 'Chuyn gia hng u v v sinh him mun. C nhiu nghin cu v kinh nghim iu tr thnh cng nhiu trng hp kh.',
      education: 'i hc Y H Ni',
      languages: ['Ting Vit', 'English', ''],
      consultationCount: 2100,
    },
  ]);

  const [consultations, setConsultations] = useState([]);

  const bookAppointment = appointmentData => {
    const newAppointment = {
      id: Date.now(),
      ...appointmentData,
      status: 'scheduled',
      createdAt: new Date(),
    };
    setAppointments(prev => [...prev, newAppointment]);
    return newAppointment;
  };

  const cancelAppointment = appointmentId => {
    setAppointments(prev =>
      prev.map(apt =>
        apt.id === appointmentId ? { ...apt, status: 'cancelled' } : apt
      )
    );
  };

  const rescheduleAppointment = (appointmentId, newDate, newTime) => {
    setAppointments(prev =>
      prev.map(apt =>
        apt.id === appointmentId
          ? { ...apt, date: newDate, time: newTime, status: 'rescheduled' }
          : apt
      )
    );
  };

  const createConsultation = consultationData => {
    const newConsultation = {
      id: Date.now(),
      ...consultationData,
      status: 'active',
      createdAt: new Date(),
      messages: [],
    };
    setConsultations(prev => [...prev, newConsultation]);
    return newConsultation;
  };

  const addMessage = (consultationId, message) => {
    setConsultations(prev =>
      prev.map(consultation =>
        consultation.id === consultationId
          ? {
              ...consultation,
              messages: [
                ...consultation.messages,
                {
                  id: Date.now(),
                  ...message,
                  timestamp: new Date(),
                },
              ],
            }
          : consultation
      )
    );
  };

  const getUpcomingAppointments = () => {
    const today = new Date();
    return appointments
      .filter(apt => new Date(apt.date) >= today && apt.status === 'scheduled')
      .sort((a, b) => new Date(a.date) - new Date(b.date));
  };

  const getAvailableCounselors = () => {
    return counselors.filter(counselor => counselor.available);
  };

  const getCounselorById = id => {
    return counselors.find(counselor => counselor.id === id);
  };

  const value = {
    appointments,
    counselors,
    consultations,
    bookAppointment,
    cancelAppointment,
    rescheduleAppointment,
    createConsultation,
    addMessage,
    getUpcomingAppointments,
    getAvailableCounselors,
    getCounselorById,
  };

  return (
    <AppointmentContext.Provider value={value}>
      {children}
    </AppointmentContext.Provider>
  );
};
