import { differenceInDays, addDays, format } from 'date-fns';

class NotificationService {
  constructor() {
    this.notifications = [];
    this.subscribers = [];
  }

  // ng k lng nghe thng bo
  subscribe(callback) {
    this.subscribers.push(callback);
    return () => {
      this.subscribers = this.subscribers.filter(sub => sub !== callback);
    };
  }

  // Gi thng bo ti cc subscribers
  notify() {
    this.subscribers.forEach(callback => callback(this.notifications));
  }

  // Yu cu quyn thng bo t browser
  async requestPermission() {
    if ('Notification' in window) {
      const permission = await Notification.requestPermission();
      return permission === 'granted';
    }
    return false;
  }

  // Hin th browser notification
  showBrowserNotification(title, options = {}) {
    if ('Notification' in window && Notification.permission === 'granted') {
      const notification = new Notification(title, {
        icon: '/favicon.ico',
        badge: '/favicon.ico',
        ...options
      });

      // T ng ng sau 5 giy
      setTimeout(() => notification.close(), 5000);
      
      return notification;
    }
  }

  // To thng bo trong app
  createInAppNotification(notification) {
    const newNotification = {
      id: Date.now() + Math.random(),
      timestamp: new Date(),
      read: false,
      ...notification
    };

    this.notifications.unshift(newNotification);
    
    // Gii hn s lng thng bo
    if (this.notifications.length > 50) {
      this.notifications = this.notifications.slice(0, 50);
    }

    this.notify();
    return newNotification;
  }

  // nh du  c
  markAsRead(notificationId) {
    const notification = this.notifications.find(n => n.id === notificationId);
    if (notification) {
      notification.read = true;
      this.notify();
    }
  }

  // nh du tt c  c
  markAllAsRead() {
    this.notifications.forEach(notification => {
      notification.read = true;
    });
    this.notify();
  }

  // Xa thng bo
  removeNotification(notificationId) {
    this.notifications = this.notifications.filter(n => n.id !== notificationId);
    this.notify();
  }

  // To thng bo chu k sinh sn
  generateCycleNotifications(cycleData) {
    if (!cycleData.lastPeriod) return [];

    const notifications = [];
    const today = new Date();
    const lastPeriodDate = new Date(cycleData.lastPeriod);

    // Tnh ton cc mc quan trng
    const nextPeriodDate = addDays(lastPeriodDate, cycleData.cycleLength);
    const ovulationDate = addDays(lastPeriodDate, cycleData.cycleLength - 14);
    const fertilityStartDate = addDays(ovulationDate, -5);
    const fertilityEndDate = addDays(ovulationDate, 1);

    const daysToNextPeriod = differenceInDays(nextPeriodDate, today);
    const daysToOvulation = differenceInDays(ovulationDate, today);

    // Thng bo rng trng
    if (daysToOvulation === 1) {
      notifications.push({
        type: 'ovulation',
        title: 'Rng trng d kin',
        message: 'Ngy mai l ngy d on rng trng. Hy theo di cc du hiu ca c th!',
        priority: 'high',
        actionText: 'Xem chi tit',
        actionUrl: '/dashboard/cycle-tracking'
      });
    }

    if (daysToOvulation === 0) {
      notifications.push({
        type: 'ovulation',
        title: 'Ngy rng trng',
        message: 'Hm nay l ngy d on rng trng. y l thi im c kh nng th thai cao nht!',
        priority: 'high',
        actionText: 'Ghi nhn triu chng',
        actionUrl: '/dashboard/cycle-tracking'
      });
    }

    // Thng bo k kinh sp ti
    if (daysToNextPeriod === 2) {
      notifications.push({
        type: 'period',
        title: 'K kinh sp ti',
        message: 'K kinh d kin bt u trong 2 ngy na. Hy chun b sn sng!',
        priority: 'medium',
        actionText: 'Chun b',
        actionUrl: '/dashboard/cycle-tracking'
      });
    }

    if (daysToNextPeriod === 1) {
      notifications.push({
        type: 'period',
        title: 'K kinh bt u ngy mai',
        message: 'Ngy mai k kinh d kin s bt u. ng qun mang theo bng v sinh!',
        priority: 'high',
        actionText: 'Chun b',
        actionUrl: '/dashboard/cycle-tracking'
      });
    }

    if (daysToNextPeriod === 0) {
      notifications.push({
        type: 'period',
        title: 'K kinh bt u hm nay',
        message: 'Hm nay l ngy d on k kinh bt u. Hy ghi nhn nu  bt u!',
        priority: 'high',
        actionText: 'Ghi nhn',
        actionUrl: '/dashboard/cycle-tracking'
      });
    }

    // Thng bo thi k mu m
    const fertilityDaysLeft = differenceInDays(fertilityEndDate, today);
    if (fertilityDaysLeft >= 0 && differenceInDays(today, fertilityStartDate) >= 0) {
      notifications.push({
        type: 'fertility',
        title: 'Thi k mu m',
        message: `Bn ang trong thi k mu m. Cn ${fertilityDaysLeft + 1} ngy.`,
        priority: 'medium',
        actionText: 'Theo di',
        actionUrl: '/dashboard/cycle-tracking'
      });
    }

    // Thng bo chm sc sc khe
    if (daysToNextPeriod <= 7 && daysToNextPeriod > 2) {
      notifications.push({
        type: 'health',
        title: 'Chm sc sc khe',
        message: 'Hy duy tr ch  n ung lnh mnh v tp th dc nh nhng  chun b cho k kinh.',
        priority: 'low',
        actionText: 'Xem li khuyn',
        actionUrl: '/dashboard/cycle-tracking'
      });
    }

    return notifications;
  }

  // Ln lch thng bo browser
  scheduleBrowserNotifications(cycleData) {
    if (!cycleData.lastPeriod) return;

    const notifications = this.generateCycleNotifications(cycleData);
    
    notifications.forEach(notification => {
      // To thng bo trong app
      this.createInAppNotification(notification);

      // Hin th browser notification cho priority cao
      if (notification.priority === 'high') {
        this.showBrowserNotification(notification.title, {
          body: notification.message,
          tag: notification.type,
          requireInteraction: true
        });
      }
    });
  }

  // Nhc nh hng ngy
  setDailyReminders(cycleData) {
    const notifications = this.generateCycleNotifications(cycleData);
    
    notifications.forEach(notification => {
      this.createInAppNotification(notification);
    });
  }

  // Thng bo khi c triu chng bt thng
  checkAbnormalSymptoms(symptoms, cycleData) {
    const concerningSymptoms = [
      'Chy mu bt thng',
      'au bng d di', 
      'St cao',
      'Kh h c mi l'
    ];

    const foundConcerningSymptoms = symptoms.filter(symptom => 
      concerningSymptoms.some(concerning => 
        symptom.toLowerCase().includes(concerning.toLowerCase())
      )
    );

    if (foundConcerningSymptoms.length > 0) {
      this.createInAppNotification({
        type: 'health_alert',
        title: 'Triu chng cn ch ',
        message: 'Bn  ghi nhn mt s triu chng c th cn c t vn y t. Hy lin h vi chuyn gia.',
        priority: 'high',
        actionText: 'T vn ngay',
        actionUrl: '/consultation'
      });

      this.showBrowserNotification('Triu chng cn ch ', {
        body: 'Bn c triu chng cn c t vn y t. Nhn  xem chi tit.',
        tag: 'health_alert',
        requireInteraction: true
      });
    }
  }

  // Thng bo khi chu k bt thng
  checkIrregularCycle(cycleHistory) {
    if (cycleHistory.length < 3) return;

    const recentCycles = cycleHistory.slice(-3);
    const cycleLengths = recentCycles.map(cycle => cycle.cycleLength);
    const avgLength = cycleLengths.reduce((sum, length) => sum + length, 0) / cycleLengths.length;
    
    const hasIrregularity = cycleLengths.some(length => 
      Math.abs(length - avgLength) > 5
    );

    if (hasIrregularity) {
      this.createInAppNotification({
        type: 'cycle_alert',
        title: 'Chu k bt thng',
        message: 'Chu k gn y ca bn c s thay i ng k. Hy tham kho  kin chuyn gia.',
        priority: 'medium',
        actionText: 'T vn',
        actionUrl: '/consultation'
      });
    }
  }

  // Ly thng k thng bo
  getNotificationStats() {
    const total = this.notifications.length;
    const unread = this.notifications.filter(n => !n.read).length;
    const byType = this.notifications.reduce((acc, n) => {
      acc[n.type] = (acc[n.type] || 0) + 1;
      return acc;
    }, {});

    return { total, unread, byType };
  }

  // Xa thng bo c
  cleanupOldNotifications(daysOld = 30) {
    const cutoffDate = new Date();
    cutoffDate.setDate(cutoffDate.getDate() - daysOld);

    this.notifications = this.notifications.filter(
      notification => notification.timestamp > cutoffDate
    );

    this.notify();
  }

  // Gi email xc nhn t lch (mock)
  sendBookingConfirmationEmail({ to, counselorName, date, time, type }) {
    // Gi lp gi email, thc t s gi API backend hoc EmailJS
    console.log(
      `Gi email xc nhn ti ${to}:\nBn  t lch t vn vi ${counselorName} vo ${date} lc ${time} (${type})`
    );
    // C th show toast hoc notification in-app nu mun
    this.createInAppNotification({
      type: 'appointment',
      title: ' gi email xc nhn',
      message: ` gi email xc nhn t lch t vn ti ${to}`,
      priority: 'low',
    });
  }

  // Gi email nhc nh lch hn (mock)
  sendAppointmentReminderEmail({ to, counselorName, date, time, type }) {
    // Gi lp gi email nhc nh, thc t s gi API backend hoc EmailJS
    console.log(
      `Gi email nhc nh ti ${to}:\nBn c lch t vn vi ${counselorName} vo ${date} lc ${time} (${type})`
    );
    this.createInAppNotification({
      type: 'appointment',
      title: ' gi email nhc nh',
      message: ` gi email nhc nh lch t vn ti ${to}`,
      priority: 'low',
    });
  }
}

// Singleton instance
const notificationService = new NotificationService();

export default notificationService; 