import React, { useState } from 'react';
import { useAppointment } from '../../../context/AppointmentContext';
import {
  MessageCircle,
  Send,
  Search,
  Filter,
  Clock,
  User,
  Star,
  Plus,
  Tag,
  X,
  Heart,
  Calendar,
  HelpCircle,
  CheckCircle,
} from 'lucide-react';
import { format } from 'date-fns';
import { toast } from 'react-toastify';
import './index.css';

const QA = () => {
  const { counselors, createConsultation, consultations, addMessage } =
    useAppointment();
  const [activeTab, setActiveTab] = useState('browse'); // browse, myQuestions, ask
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [searchQuery, setSearchQuery] = useState('');
  const [showQuestionDetail, setShowQuestionDetail] = useState(false);
  const [showAskQuestion, setShowAskQuestion] = useState(false);
  const [selectedQuestion, setSelectedQuestion] = useState(null);
  const [newQuestion, setNewQuestion] = useState({
    title: '',
    content: '',
    category: 'general',
    isAnonymous: false,
  });

  const categories = [
    { id: 'all', name: 'Tt c' },
    { id: 'general', name: 'Tng qut' },
    { id: 'contraception', name: 'Trnh thai' },
    { id: 'menstruation', name: 'Kinh nguyt' },
    { id: 'pregnancy', name: 'Mang thai' },
    { id: 'sti', name: 'STIs' },
    { id: 'mental-health', name: 'Sc khe tm l' },
  ];

  const sampleQuestions = [
    {
      id: 1,
      title: 'Chu k kinh nguyt khng u c nguy him khng?',
      content:
        'Em 22 tui, chu k kinh nguyt ca em thng khng u, c khi 25 ngy c khi 35 ngy. Em c cn lo lng khng ?',
      category: 'menstruation',
      author: 'n danh',
      date: '2024-01-15',
      answers: 3,
      views: 124,
      status: 'answered',
      isAnswered: true,
      expertAnswer: {
        counselor: 'Dr. Nguyn Th Hng',
        content:
          'Chu k kinh nguyt t 21-35 ngy u c coi l bnh thng. Tuy nhin, nu c s thay i t ngt hoc km theo triu chng bt thng, bn nn thm khm  c t vn c th.',
        date: '2024-01-16',
      },
    },
    {
      id: 2,
      title: 'Thuc trnh thai khn cp c tc dng ph g?',
      content:
        'Cho bc s, em mun hi v thuc trnh thai khn cp. Em c cn lu  g khng ?',
      category: 'contraception',
      author: 'Mai A.',
      date: '2024-01-14',
      answers: 2,
      views: 89,
      status: 'answered',
      isAnswered: true,
      expertAnswer: {
        counselor: 'Dr. L Vn Minh',
        content:
          'Thuc trnh thai khn cp c th gy mt s tc dng ph nh bun nn, au u, ri lon kinh nguyt. Khng nn s dng qu 2 ln/thng.',
        date: '2024-01-15',
      },
    },
    {
      id: 3,
      title: 'Lm th no  phng nga nhim trng ng tit niu?',
      content:
        'Em hay b nhim trng ng tit niu, c cch phng nga no khng ?',
      category: 'general',
      author: 'Linh N.',
      date: '2024-01-13',
      answers: 1,
      views: 67,
      status: 'pending',
      isAnswered: false,
    },
  ];

  const handleSubmitQuestion = () => {
    if (!newQuestion.title.trim() || !newQuestion.content.trim()) {
      toast.error('Vui lng in y  thng tin');
      return;
    }

    // Mock submission
    toast.success(
      'Cu hi  c gi thnh cng! Chng ti s phn hi trong vng 24h.'
    );

    setNewQuestion({
      title: '',
      content: '',
      category: 'general',
      isAnonymous: false,
    });

    setActiveTab('myQuestions');
  };

  const filteredQuestions = sampleQuestions.filter(question => {
    const matchesCategory =
      selectedCategory === 'all' || question.category === selectedCategory;
    const matchesSearch =
      question.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
      question.content.toLowerCase().includes(searchQuery.toLowerCase());
    return matchesCategory && matchesSearch;
  });

  const handleQuestionClick = question => {
    setSelectedQuestion(question);
    setShowQuestionDetail(true);
  };

  const handleAskQuestion = () => {
    setShowAskQuestion(true);
  };

  return (
    <div className="qa-container">
      {/* Header */}
      <div className="qa-header">
        <div className="qa-header-content">
          <h1 className="qa-title">Hi p</h1>
          <p className="qa-subtitle">
            t cu hi v nhn t vn t cc chuyn gia ca chng ti
          </p>
        </div>
      </div>

      <div className="qa-main">
        {/* Search Section */}
        <div className="search-section">
          <div className="search-container">
            <Search className="search-icon" />
            <input
              type="text"
              placeholder="Tm kim cu hi..."
              value={searchQuery}
              onChange={e => setSearchQuery(e.target.value)}
              className="search-input"
            />
          </div>
          <button onClick={handleAskQuestion} className="ask-question-btn">
            <Plus className="w-5 h-5 mr-2" />
            t cu hi mi
          </button>
        </div>

        {/* Tabs */}
        <div className="qa-tabs">
          <button
            className={`tab-button ${activeTab === 'browse' ? 'active' : ''}`}
            onClick={() => setActiveTab('browse')}
          >
            Tt c cu hi
          </button>
          <button
            className={`tab-button ${
              activeTab === 'my-questions' ? 'active' : ''
            }`}
            onClick={() => setActiveTab('my-questions')}
          >
            Cu hi ca ti
          </button>
        </div>

        {/* Categories */}
        <div className="categories-section">
          <h2 className="section-title">Danh mc</h2>
          <div className="categories-grid">
            <div
              className={`category-card ${
                selectedCategory === '' ? 'selected' : ''
              }`}
              onClick={() => setSelectedCategory('')}
            >
              <HelpCircle className="category-icon" />
              <h3 className="category-name">Tt c</h3>
              <p className="category-count">{sampleQuestions.length} cu hi</p>
            </div>
            {categories.map(category => (
              <div
                key={category.id}
                className={`category-card ${
                  selectedCategory === category.id ? 'selected' : ''
                }`}
                onClick={() => setSelectedCategory(category.id)}
              >
                <HelpCircle className="category-icon" />
                <h3 className="category-name">{category.name}</h3>
                <p className="category-count">
                  {
                    filteredQuestions.filter(q => q.category === category.id)
                      .length
                  }{' '}
                  cu hi
                </p>
              </div>
            ))}
          </div>
        </div>

        {/* Questions List */}
        <div className="questions-section">
          <h2 className="section-title">
            {selectedCategory === 'all'
              ? 'Tt c cu hi'
              : categories.find(c => c.id === selectedCategory)?.name}
          </h2>
          {filteredQuestions.length > 0 ? (
            <div className="questions-list">
              {filteredQuestions.map(question => (
                <div
                  key={question.id}
                  className="question-item"
                  onClick={() => handleQuestionClick(question)}
                >
                  <div className="question-header">
                    <h3 className="question-title">{question.title}</h3>
                    <div className="question-status">
                      <span className={`status-badge ${question.status}`}>
                        {question.status === 'answered'
                          ? ' tr li'
                          : 'Ch tr li'}
                      </span>
                    </div>
                  </div>
                  <p className="question-preview">{question.content}</p>
                  <div className="question-meta">
                    <div className="meta-item">
                      <User className="meta-icon" />
                      <span>{question.author}</span>
                    </div>
                    <div className="meta-item">
                      <Calendar className="meta-icon" />
                      <span>
                        {format(new Date(question.date), 'dd/MM/yyyy')}
                      </span>
                    </div>
                    <div className="meta-item">
                      <Tag className="meta-icon" />
                      <span>
                        {categories.find(c => c.id === question.category)?.name}
                      </span>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div className="empty-state">
              <HelpCircle className="empty-state-icon" />
              <h3>Khng tm thy cu hi no</h3>
              <p>Th thay i t kha tm kim hoc danh mc</p>
            </div>
          )}
        </div>

        {/* Question Detail Modal */}
        {showQuestionDetail && selectedQuestion && (
          <div className="question-detail-overlay">
            <div className="question-detail-container">
              <div className="question-detail-header">
                <h2 className="question-detail-title">
                  {selectedQuestion.title}
                </h2>
                <button
                  onClick={() => setShowQuestionDetail(false)}
                  className="close-btn"
                >
                  <X />
                </button>
              </div>

              <div className="question-content">
                <p className="question-text">{selectedQuestion.content}</p>
              </div>

              <div className="question-meta">
                <div className="meta-item">
                  <User className="meta-icon" />
                  <span>{selectedQuestion.author}</span>
                </div>
                <div className="meta-item">
                  <Calendar className="meta-icon" />
                  <span>
                    {format(new Date(selectedQuestion.date), 'dd/MM/yyyy')}
                  </span>
                </div>
                <div className="meta-item">
                  <Tag className="meta-icon" />
                  <span>
                    {
                      categories.find(c => c.id === selectedQuestion.category)
                        ?.name
                    }
                  </span>
                </div>
              </div>

              {selectedQuestion.expertAnswer ? (
                <div className="answer-section">
                  <h3 className="answer-title">Cu tr li t chuyn gia</h3>
                  <div className="answer-content">
                    <p className="answer-text">
                      {selectedQuestion.expertAnswer.content}
                    </p>
                    <div className="answer-author">
                      <div className="author-avatar">
                        {selectedQuestion.expertAnswer.counselor.charAt(0)}
                      </div>
                      <span>{selectedQuestion.expertAnswer.counselor}</span>
                      <span></span>
                      <span>
                        {format(
                          new Date(selectedQuestion.expertAnswer.date),
                          'dd/MM/yyyy'
                        )}
                      </span>
                    </div>
                  </div>
                </div>
              ) : (
                <div className="no-answer">
                  <Clock className="no-answer-icon" />
                  <h4>Cha c cu tr li</h4>
                  <p>
                    Chuyn gia s tr li cu hi ca bn trong thi gian sm
                    nht
                  </p>
                </div>
              )}
            </div>
          </div>
        )}

        {/* Ask Question Modal */}
        {showAskQuestion && (
          <div className="ask-question-overlay">
            <div className="ask-question-container">
              <div className="ask-question-header">
                <h2 className="ask-question-title">t cu hi mi</h2>
                <button
                  onClick={() => setShowAskQuestion(false)}
                  className="close-btn"
                >
                  <X />
                </button>
              </div>

              <div className="form-section">
                <h3 className="form-section-title">Tiu  cu hi</h3>
                <input
                  type="text"
                  value={newQuestion.title}
                  onChange={e =>
                    setNewQuestion(prev => ({
                      ...prev,
                      title: e.target.value,
                    }))
                  }
                  placeholder="Nhp tiu  ngn gn cho cu hi..."
                  className="form-input"
                />
              </div>

              <div className="form-section">
                <h3 className="form-section-title">Danh mc</h3>
                <select
                  value={newQuestion.category}
                  onChange={e =>
                    setNewQuestion(prev => ({
                      ...prev,
                      category: e.target.value,
                    }))
                  }
                  className="category-select"
                >
                  <option value="">Chn danh mc</option>
                  {categories.map(category => (
                    <option key={category.id} value={category.id}>
                      {category.name}
                    </option>
                  ))}
                </select>
              </div>

              <div className="form-section">
                <h3 className="form-section-title">Ni dung cu hi</h3>
                <textarea
                  value={newQuestion.content}
                  onChange={e =>
                    setNewQuestion(prev => ({
                      ...prev,
                      content: e.target.value,
                    }))
                  }
                  placeholder="M t chi tit cu hi ca bn..."
                  className="form-textarea"
                />
                <p className="form-note">
                  Hy m t cng chi tit cng tt  chuyn gia c th t vn
                  chnh xc nht
                </p>
              </div>

              <div className="form-actions">
                <button
                  onClick={() => setShowAskQuestion(false)}
                  className="form-btn cancel"
                >
                  Hy
                </button>
                <button
                  onClick={handleSubmitQuestion}
                  className="form-btn submit"
                  disabled={
                    !newQuestion.title ||
                    !newQuestion.content ||
                    !newQuestion.category
                  }
                >
                  Gi cu hi
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default QA;
